
import DashboardView from '@/components/admin/dashboard/dashboardView';
import Dashboard from '@/components/admin/dashboard/dashboard';

import LevelIndex from '@/components/admin/setting/level/index';

import ClassIndex from '@/components/admin/setting/class/index';

import SubjectIndex from '@/components/admin/setting/subject/index';

import ClassSubjectIndex from '@/components/admin/setting/classSubject/index';

import Session from '@/components/admin/setting/session/index';

import TermType from '@/components/admin/setting/termType/index';

import Term from '@/components/admin/setting/term/index';

import ScratchCard from '@/components/admin/setting/scratchCard';

import AddResult from '@/components/admin/result/addResult';

import GenerateResultIndex from '@/components/admin/result/generateResult/index';

import ViewGeneratedResultIndex from '@/components/admin/result/viewGeneratedResult/index';

import StudentIndex from '@/components/admin/student/index';
import StudentAdd from '@/components/admin/student/createStudent';
import CreateMultipleStudent from '@/components/admin/student/createMultipleStudents';

import CreateStaff from '@/components/admin/staff/createStaff';

import ParentBulkSms from '@/components/admin/parent/sendBulkSms';

export default [
    {
        path: '/admin',
        name: 'adminDashboardview',
        component: DashboardView,
        meta: {
            ForAdmin: true,
        },
        children: [
            {
                path: 'dashboard',
                name: 'dashboard',
                component: Dashboard,
            },
            // Level routes
            {
                path: 'level',
                name: 'level_index',
                component: LevelIndex,
            },
            // Class routes
            {
                path: 'class',
                name: 'class_index',
                component: ClassIndex,
            },
            // Subject routes
            {
                path: 'subject',
                name: 'subject_index',
                component: SubjectIndex,
            },
            // Class Subject routes
            {
                path: 'classSubject',
                name: 'class_subject_index',
                component: ClassSubjectIndex,
            },
            // Seesion routes
            {
                path: 'session',
                name: 'session_index',
                component: Session,
            },

            // Term Type routes
            {
                path: 'termType',
                name: 'term_type_index',
                component: TermType,
            },

            // Term routes
            {
                path: 'term',
                name: 'term_index',
                component: Term,
            },
            // Scratch Card routes
            {
                path: 'scratchCard',
                name: 'scratch_card_index',
                component: ScratchCard,
            },
            // Result routes
            {
                path: 'results/add',
                name: 'result_add',
                component: AddResult,
            },

            // Generate Result routes
            {
                path: 'results/generate',
                name: 'results_generate',
                component: GenerateResultIndex,
            },
            // View Generated Result routes
            {
                path: 'results/generated-lists',
                name: 'view_results_generated',
                component: ViewGeneratedResultIndex,
            },
            // Students routes
            {
                path: 'students',
                name: 'students_index',
                component: StudentIndex,
            },
            {
                path: 'student/add',
                name: 'student_add',
                component: StudentAdd,
            },
            {
                path: 'student/multiple',
                name: 'student_multiple',
                component: CreateMultipleStudent,
            },
            // Staff Routes
            {
                path: 'staff/add',
                name: 'staff_add',
                component: CreateStaff,
            },

            // Parent Routes
            {
                path: 'parent/message',
                name: 'parent_message',
                component: ParentBulkSms,
            },
        ],
    },
];



