<template>
<div style="position:absolute; width: 100%; height: 100%;">
  <div class="d-flex align-items-stretch min-height-100 h-100" >
    <div class="bg-cover bg-2 hidden-sm-down col-md-6 col-lg-8"></div>
    <div class="card card-block mb-0 rounded-0 p-5 col-sm-12 col-md-6 col-lg-4">
      <form @submit.prevent="signIn" method="POST">
        <!-- <form [formGroup]="form" (ngSubmit)=" login(form)"> -->
        <div class="mb-3 text-center">
          <img src="/../static/images/logo1.png" class="avatar-lg mb-1" alt="">
          <br/><br/><br/>
          <h3 class="ff-headers ">Sign in to your account</h3>
        </div>
        <fieldset class="form-group">
        <!-- <fieldset class="form-group" [ngClass]="{'has-danger': form.controls['uname'].hasError('required') && form.controls['uname'].touched}"> -->
          <!-- <input type="text" class="form-control form-style" placeholder="username" [formControl]="form.controls['uname']" [ngClass]="{'form-control-danger': form.controls['uname'].hasError('required') && form.controls['uname'].touched}"/> -->

          <input type="text" class="form-control form-style" v-model="form_data.username" placeholder="username" />
          <!-- <div class="form-control-feedback" *ngIf="form.controls['uname'].hasError('required') && form.controls['uname'].touched">Username is required.</div> -->

          <!-- <div class="form-control-feedback">Username is required.</div> -->
        </fieldset>
        <br/>
        <fieldset class="form-group">
        <!-- <fieldset class="form-group" [ngClass]="{'has-danger': form.controls['password'].hasError('required') && form.controls['password'].touched}"> -->
          <!-- <input type="password" class="form-control form-style" placeholder="Password" [formControl]="form.controls['password']" [ngClass]="{'form-control-danger': form.controls['password'].hasError('required') && form.controls['password'].touched}"/> -->

          <input type="password" class="form-control form-style" v-model="form_data.password" placeholder="Password" />
          <!-- <div class="form-control-feedback" *ngIf="form.controls['password'].hasError('required') && form.controls['password'].touched">Password is required.</div> -->
          <!-- <a [routerLink]="['/authentication/forgot']" class="form-text"><small>Forgot password</small></a> -->
          <!-- <a [routerLink]="['/authentication/parent']" class="form-text"><small>Click Here to verify if you are a parent</small></a> -->
        </fieldset>

        <div class="block"></div>
        <br />
        <div class="text-center">
        <!-- <button class="btn btn-primary   btn-submit " id="btn" [class.disabled]="loading" #btn (click)="loading = true" type="submit" [disabled]="!form.valid" >Login</button> -->

        <!-- <button class="btn btn-primary btn-submit " id="btn" type="submit" >Login</button> -->
        <submit-button class="btn btn-primary btn-submit" :loading="isLoading">Login</submit-button>

        <br />
        <div v-if="isLoading">
            <div  class="row">
                <div class="col-md-12 text-center">
                  <img style="height:100%;width:60%"  src="/../static/images/login.gif"/>
                </div>
              </div>
        </div>



        </div>
      </form>
    </div>
  </div>
</div>
</template>
<script>
import { mapMutations }  from 'vuex';
import SubmitButton from '@/components/utils/submitButton';

export default {
  data () {
    return {
      isLoading: false,
      error: '',
      form_data: {
        username: '',
        password: '',
      }
    }
  },
  methods: {
    signIn() {
      this.isLoading = true;
      this.$http.post('account/login', this.form_data)
      .then((response) => {
        if(response){
          this.logIn({ token: response.access_token, user: response });
          this.isLoading = false;
          if(!response.error) {
            this.checkUser(response);
            // this.$router.replace({ path: '/admin/dashboard' });
          } else {
            this.error = response.error;
            console.log(this.error);
          }

        }
      })
      .catch(err => {
        console.log('Caught error ',err);
      })
    },
    checkUser(response){
      let userRole = response.role;
      if(userRole == 'Admin'){
        this.$router.replace({ path: '/admin/dashboard' });
      } else if (userRole == 'Student') {
        this.$router.replace({ path: '/student/dashboard' });
      } else {
        this.$router.replace({ path: '/admin/dashboard' });
      }
    },
    ...mapMutations({
        logIn: 'signIn',
        // updateMessage: 'successMessage',
      }),
  },
  components: {
      SubmitButton,
    },

}

</script>

<style lang="scss" scoped>

  @import './static/styles/signin.scss';
  // @import '@/static/styles/scss/components/sts';
  // @import './static/styles/signin';

</style>



