<template>
    <tr>
        <td class="text-center">{{index + 1}}</td>
        <td class="text-center">{{klass.name}}</td>


        <td class="text-center">{{klass.level.name}}</td>

        <td class="text-center">{{klass.formTeacherName}}</td>

        <td class="text-center"><span v-if="klass.isActive">
                <i class="fa fa-check-square-o fa-2x text-success"></i>
            </span>
            <span v-else>
                <i class="fa fa-times-circle fa-2x text-danger"></i>
            </span>
        </td>

        <td class="text-center">
            <submit-button class="btn btn-sm btn-round btn-primary" @click="launchEditModal(klass.id)" :loading="editLoading">EDIT</submit-button>
        </td>
        <td class="text-center">
            <submit-button class="btn btn-sm btn-round btn-danger" :loading="deleteLoading" @click="deleteClass(klass.id)">DELETE</submit-button>
        </td>
        
    </tr>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
import EditModal from '@/components/admin/setting/class/editModal';


    export default {
        props: ['index','klass'],
        data() {
            return {
                editLoading: false,
                deleteLoading: false,
                
            }
        },
        methods: {
            launchEditModal(id){
                this.editLoading = true;
                this.$http.get(`utilities/classes/${id}`)
                .then(response => {
                    
                    this.$emit('editClass', response);
                    window.$('.edit_modal').modal('show');
                    this.editLoading = false;
                })
                .catch(err => {
                    this.$emit('editClass', err);
                    this.editLoading = false;
                })
            },
            deleteClass(id){
                 if(confirm('Are you sure, you want to delete this class')){
                    this.deleteLoading = true;
                    this.$http.delete(`settings/class/delete/${id}`)
                    .then((response) => {
                        this.$emit('deleteClass', response);
                        this.deleteLoading = false; 
                    })
                    .catch(err => {
                        this.$emit('deleteClass', err);
                        this.deleteLoading = false;
                    })
                }
            }
        },
        components: {
            SubmitButton,
            EditModal
        }
    }
</script>


