<template>
    
    <div class="">
        <div class="page-title">
            <div class="title_left">
               
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" :disabled="loadingTerm" data-toggle="modal" data-target=".card_term">
                        GENERATE CARD FOR TERM <i v-if="loadingTerm" class="fa fa-spin fa-spinner"></i>
                    </button>
                    <modal-card-term :initialTerms="terms" @cardModalMessage="cardModalMessage($event)"></modal-card-term>
                </div>
                <hr>
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" :disabled="loadingTerm" data-toggle="modal" data-target=".card_session">
                        GENERATE CARD FOR SESSION <i v-if="loadingSession" class="fa fa-spin fa-spinner"></i>
                    </button>
                    <modal-card-session :initialSessions="sessions" @sessionModalMessage="sessionModalMessage($event)"></modal-card-session>
                </div>
                

            </div>
            
        </div>
        <div class="clearfix"></div>

        <div class="row" v-if="scratchCards">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_content">
                        <div v-if="message">
                            <p class="alert alert-success text-center">{{message}}</p>
                        </div>
                        <p v-if="error" class="alert alert-danger text-center">{{error}}</p>
                        <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                        <loading v-if="loading"></loading>
                        
                        <card-row v-if="scratchCards" :initialScratchCards="scratchCards" :initialHeading="heading"></card-row>
                        
                    </div>
                </div>    
            </div>    
        </div>

        <div class="clearfix"></div>
    </div>

    
</template>
<script>
import ModalCardTerm from '@/components/admin/setting/scratchCard/modalCardTerm';
import ModalCardSession from '@/components/admin/setting/scratchCard/modalCardSession';
import CardRow from '@/components/admin/setting/scratchCard/cardRow';
import SubmitButton from '@/components/utils/submitButton';
import Loading from '@/components/utils/loading';

export default {
    data () {
        return {
            loading: false,
            loadingSession: false,
            loadingTerm: false,
            responseErrors: '',
            error: '',
            message: '',
            terms: '',
            scratchCards: '',
            sessions: '',
            heading: '',
        }
    },
    methods: {
        getAllTerms(){
            this.loadingTerm = true;
            this.$http.get(`utilities/terms/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.terms = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loadingTerm = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loadingTerm = false;
           })
        },
        getSessions(){
           this.loadingSession = true;
           this.$http.get(`utilities/sessions/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.sessions = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loadingSession = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loadingSession = false;
           })
        },
        cardModalMessage(response){
            this.heading = '';
            this.scratchCards = '';
            if(response.status == 'success'){
                this.scratchCards = response.data;
                this.heading = 'Term Scratch Cards generated';
                this.clearMessage();
            } else {
                this.error = 'Term Scratch Card could not be generated';
            }
        },
        sessionModalMessage(response){
            this.heading = '';
            this.scratchCards = '';
            if(response.status == 'success'){
                this.scratchCards = response.data;
                this.heading = 'Session Scratch Cards generated';
                this.clearMessage();
            } else {
                this.error = 'Session Scratch Card could not be generated';
            }
        },
        
        clearMessage(){
            setTimeout(() => {
                this.message = '';
                this.error = '';
                this.responseErrors = '';
            }, 10000);
        }

    },
    created(){
       this.getAllTerms();
       this.getSessions();

    },
    components: {
        ModalCardTerm,
        ModalCardSession, 
        Loading,
        // EditModal,
        SubmitButton,
        CardRow
    }
}
    
</script>

<style src="@/assets/datatables.css"></style>



