<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Add New Class</h4>
                    </div>
                    <p v-if="errorMessage" class="alert alert-danger text-center">{{errorMessage}}</p>
                    <form @submit.prevent="submitLevel" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                        <div class="form-group">
                            <label for="name">Name <span class="required">*</span>
                            </label>
                            <input type="text" v-model="formData.name" id="name" required class="form-control btn-round">
                        </div>
                        <div class="form-group">
                            <label for="level">Level <span class="required">*</span></label>
                            
                            <select class="form-control btn-round" v-model="formData.level" id="level">
                                <option value=""><span v-if="loadingLevel"><i class="fa fa-spinner"></i> Loading..</span><span v-else>Choose Level</span> </option>
                                <option v-for="(level, index) in levels" :key="index" :value="level.id">{{level.name}}</option> 
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="name">Form Teacher Name <span class="required">*</span>
                            </label>
                            <input type="text" v-model="formData.formTeacherId" id="name" class="form-control btn-round">
                        </div>
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="loading" class="btn btn-round btn-sm btn-primary">Submit</submit-button>
                    </div>
                    </form> 
                    
                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

export default {
    data() {
        return {
            loadingLevel: false,
            loading: false,
            name: '',
            levels: {},
            responseErrorLevel: '',
            errorMessage: '',
            formData: {
                name: '',
                level: '',
                formTeacherId: '',
            }
        }
    },
    methods: {
        getLevels(){
           this.loadingLevel = true;
           this.$http.get(`utilities/levels/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.levels = response.data;
                   } else {
                       this.responseErrorLevel = response.errors;
                   }
                   this.loadingLevel = false;
               }
           })
           .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.loadingLevel = false;
           })
        },
        submitLevel(){
            this.loading = true;
            this.$http.post('settings/class/add', this.assignValues())
            .then(response => {
                if(response) {
                   this.$emit('modalMessage', response);
                   window.$('.bs-example-modal-lg').modal('hide'); 
                   this.loading = false;
                }
                
            })
            .catch(err => {
                errorMessage = err;
                this.loading = false;
            })
        },
        getLevelFromLevels(){
            return this.levels.find(item => item.id == this.formData.level );
        },
        assignValues(){
            let obj = {
                name: this.formData.name,
                level: {
                    id: this.formData.level
                },
                formTeacherId: this.formData.formTeacherId
            }
            return obj;
            // let level = this.getLevelFromLevels();
            // let obj = {
            //     level: {
            //         id: level.id,
            //         name: level.name
            //     },
            //     name: this.formData.name,
            //     isActive: true,

            // }
        }
    },

    created(){
        this.getLevels();
    },
    components: {
        SubmitButton,
    }
}
</script>


