<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade changeClass_modal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Change Student Class</h4>
                    </div>
                    
                    <form @submit.prevent="changeClass" data-parsley-validate class="form-horizontal form-label-left" >
                        <div class="modal-body">
                            <p v-if="errors" class="alert alert-danger text-center">
                                <span v-for="(error, index) in errors" :key="index">{{error}}&nbsp;</span>
                            </p>
                            <div class="" v-if="studentClassDetails !== ''">
                                <table class="table table-borderless">
                                    <tr>
                                        <td><h4>Name</h4></td>
                                        <td><strong>{{student.fullName}}</strong> </td>
                                    </tr>
                                    <tr class="">
                                        <td><h4>Student's Class</h4></td>
                                        <td><strong>{{student.class.name}}</strong></td>
                                    </tr>
                                    <tr>
                                        <td><h6>Reg Number</h6></td>
                                        <td><h6><strong>{{student.regNo}}</strong></h6></td>
                                    </tr>
                                    
                                </table>
                                
                            </div>
                            <div class="form-group">
                                <label for="level">Level <span class="required"> *</span> </label>
                                <select class="form-control btn-round" @change="getLevelClass()" v-model="studentClassDetails.level.id" id="level">
                                    <option value=""><span v-if="loadingLevel"><i class="fa fa-spinner"></i> Loading ..</span><span v-else>-- Choose Level --</span></option>
                                    <option v-for="(level, index) in levels" :key="index" :value="level.id">{{level.name}}</option>
                                </select>
                            </div>
                            <div class="form-group">
                                <label for="klass">Class<span class="required"> *</span></label>
                                <select class="form-control btn-round" v-model="levelClass" id="klass">
                                    <option value=""><span v-if="loadingLevelClass"><i class="fa fa-spinner"></i> Loading ..</span><span v-else>-- Select Class you want to change to --</span></option>
                                    <option v-for="(levelClass, index) in levelClasses" :key="index" :value="levelClass.id" >{{levelClass.fullname}}</option>
                                </select>
                            </div>
                            <div class="clearfix"></div>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-sm btn-round btn-default" data-dismiss="modal">Close</button>
                            <submit-button :loading="isLoading" class="btn btn-sm btn-round btn-primary">Submit</submit-button>
                        </div>
                    </form> 

                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
    export default {
        props:['initialStudent', 'initialClassDetails'],
        data(){
            return {
                loadingLevel: false,
                loadingLevelClass: false,
                isLoading: false,
                classes: '',
                levels: '',
                level: '',
                errors: '',
                levelClasses: '',
                levelClass: '',
                responseErrors: '',
            }
        },
        computed: {
            student() {
                const student = Object.assign({}, this.initialStudent);
                student.dateOfBirth = this.formatDate(student.dateOfBirth);
                if(!student.class){
                    student.class = {
                        id: null
                    };
                }
                return student;
            },
            studentClassDetails(){
                const studentClassDetails = Object.assign({}, this.initialClassDetails);
                if(!studentClassDetails.level){
                    studentClassDetails.level = {
                        id: null
                    };
                }
                return studentClassDetails;
            }
        },
        methods:{
            //Get the levels
            getLevels(){
                this.loadingLevel = true;
                this.$http.get(`utilities/levels/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.levels = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingLevel = false;
                    }
                })
                .catch(err => {
                    this.error = '';this.error = err;this.loadingLevel = false;
                })
            },
            //On change get the class
            getLevelClass(){
                
                if(this.studentClassDetails.level.id == null || this.studentClassDetails.level.id == ''){
                    this.levelClasses = '';
                    this.levelClass = '';
                } else {
                    
                    this.levelClasses = '';
                    this.levelClass = '';
                    this.loadingLevelClass = true;
                    this.$http.get(`utilities/classes/?levelId=${this.studentClassDetails.level.id}`) 
                    .then(response => {
                        if(response){
                            if(response.status == 'success'){
                                this.levelClasses = response.data;
                            } else {
                                this.responseErrors = response.errors;
                            }
                            this.loadingLevelClass = false;
                        }
                    })
                    .catch(err => {
                        this.error = '';this.error = err;this.loadingLevelClass = false;
                    }) 
                }
            },
            formatDate(dateOfBirth) {
                const date = new Date(dateOfBirth);
                let dd = date.getDate();
                let mm = date.getMonth() + 1;
                const yyyy = date.getFullYear();
                if (dd < 10) {
                    dd = `0${dd}`;
                }
                if (mm < 10) {
                    mm = `0${mm}`;
                }
                const formattedDate = `${yyyy}-${mm}-${dd}`;
                // const formattedDate = `${mm}-${dd}-${yyyy}`;
                return formattedDate;
            },
            changeClass(){
                this.isLoading = true;
                this.$http.post(`students/changeClass`,this.assignValues())
                .then(response => {
                    if(response.status == 'success'){
                        let newClassDetails = this.levelClasses.find(item => item.id == this.levelClass);    
                        this.$emit('response', newClassDetails);
                        window.$('.changeClass_modal').modal('hide');
                    } else {
                        this.errors = response.errors;
                        this.clearMessage();
                    }
                    this.isLoading = false;
                })
            },
            assignValues(){
                let obj = {
                    regNo: this.student.regNo,
                    classId: this.levelClass,
                }
                return obj;
            },
            clearMessage(){
                setTimeout(() => {
                    this.message = '';
                    this.errors = '';
                    this.responseErrors = '';
                }, 10000);
            },
            
        },
        
        created(){
            this.getLevels();
            
        },
        watch:{
            studentClassDetails: function(){
                this.getLevelClass();
            }
        },
        components: {
            SubmitButton
        }
    }
</script>


