<template>
    <div class="">
        <div class="page-title">
            <div class="title_left">
                <div class="x_content">
                    <h3>Create Multiple Students</h3>
                </div>

            </div>
        </div>
        <div class="clearfix"></div>
        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_content">
                        <button class="btn btn-round btn-primary">DOWNLOAD EXCEL TEMPLATE</button>
                    </div>
                </div>
            </div>

        </div>
        <div class="clearfix"></div>
        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_content">
                        <div class="x_content">
                            <div v-if="message">
                                <p class="alert alert-success text-center">{{message}}</p>
                            </div>
                            <p v-if="errors" class="alert alert-danger text-center">{{errors}}</p>
                            <p v-if="responseErrors" class="alert alert-danger text-center">
                                <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                            </p>
                        </div>
                        <form @submit.prevent="uploadStudents" data-parsley-validate enctype="multipart/form-data" ref="form" class="form-horizontal form-label-left">
                            <div class="form-group">
                                <div class="col-sm-5">
                                    <label for="subject">Class</label>
                                
                                    <select class="form-control btn-round" v-model="classOne" id="klass">
                                        <option value=""><span v-if="loadingClasses"> Loading..</span><span v-else>Choose Class</span>  </option>
                                        <option v-for="(studentClass, index) in classes" :value="studentClass" :key="index">{{studentClass.fullname}}</option> 
                                    </select>
                                </div>
                            </div>
                            <div class="clearfix"></div>
                            <br>
                            <div class="form-group">
                                <div class="col-sm-5">
                                    <label for="subject">Choose Excel File</label>
                                    <input type="file" ref="file" @change="uploadedFile" class="form-control btn-round" />
                                </div>
                            </div>
                            <div class="clearfix"></div>
                            <br>
                            <div class="form-group">
                                <div class="col-sm-5">
                                    <submit-button class="btn btn-round btn-primary" :loading="uploading" :disabled="!classOne.id" type="submit">Submit</submit-button>
                                </div>
                            </div>

                        </form>
                    </div>
                </div>
            </div>

        </div>

    </div>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
    export default {
        data() {
            return {
                loadingClasses: false,
                classes: '',
                classOne: '',
                uploading: false,
                message: '',
                responseErrors: '',
                errors: '',
                formData:{
                    classId: '',
                    uploadFile: '',
                }
            }
        },
        methods: {
            getAllClasses(){
                this.loadingClasses = true;
                this.$http.get(`utilities/classes/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.classes = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingClasses = false;
                    }
                })
                .catch(err => {
                    this.error = '';this.errors = err;this.loadingClasses = false;
                })
            },
            uploadedFile(){
                this.formData.uploadFile = this.$refs.file.files[0];
            },
            uploadStudents(){
                this.uploading = true;
                let form = new FormData();
                form.append('classId', this.classOne.id);
                form.append('file', this.formData.uploadFile);
                this.$http.post(`students/multiple`, form)
                .then(response => {
                    console.log('Response form file upload ', response);
                    if(response.status == 'failed'){
                        this.responseErrors = response.errors
                    } else if(response.status == null) {
                        this.responseErrors = 'Something Went wrong';
                    } else {
                        this.message = 'Students uploaded successfully';
                    }
                    this.uploading = false;
                    this.clearMessage();
                })
            },
            clearMessage(){
                setTimeout(() => {
                    this.message = '';
                    this.errors = '';
                    this.responseErrors = '';
                }, 10000);
            },
        },
        created(){
            this.getAllClasses();
        },
        components:{
            SubmitButton
        }
    }
</script>


