<template>
    
    <div class="">
        <div class="page-title">
            <div class="title_left">
               
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" data-toggle="modal" data-target=".bs-example-modal-lg">ADD NEW LEVEL</button>
                    <modal @modalMessage="getLevelsNew($event)"></modal>
                </div>
                
            </div>
            
        </div>

        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    
                    <div class="x_title">
                        <h2>
                            Levels
                        </h2>

                        <div class="clearfix"></div>
                    </div>
                    <div v-if="message">
                        <p class="alert alert-success text-center">{{message}}</p>
                    </div>
                    <p v-if="error" class="alert alert-danger text-center">{{error}}</p>
                    <p v-if="responseErrors" class="alert alert-danger text-center">
                        <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                    </p>
                    <div class="x_panel">
                        <div v-if="loading" class="col-sm-12" style="min-height: 60px;">
                            <loading style="height:50px;"></loading>
                        </div>
                        <div class="x_content" v-if="levels">
                            <table id="datatable" class="table table-striped table-bordered">
                                <thead>
                                    <tr>
                                        <th class="text-center">S/N</th>
                                        <th class="text-center">Name</th>
                                        <th class="text-center">Edit</th>
                                        <th class="text-center">Delete</th>
                                    </tr>
                                </thead>

                                <tbody>
                                    <!-- <tr>
                                        
                                    </tr> -->
                                    <level-row v-for="(level, index) in levels" :key="index" :level="level" :index="index" @editLevel="launchEditModal($event)" @deleteLevel="deleteLevel($event)"></level-row>
                                    
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <th class="text-center">S/N</th>
                                        <th class="text-center">Name</th>
                                        <th class="text-center">Edit</th>
                                        <th class="text-center">Delete</th>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <edit-modal :level="level" @response="modalMessage($event)"></edit-modal>
    </div>

    
</template>
<script>
import Modal from '@/components/admin/setting/level/modal';
import EditModal from '@/components/admin/setting/level/editModal';
import LevelRow from '@/components/admin/setting/level/levelRow';
import SubmitButton from '@/components/utils/submitButton';
import Loading from '@/components/utils/loading';

export default {
    data () {
        return {
            loading: false,
            responseErrors: '',
            error: '',
            levels: {},
            level: '',
            message: '',
        }
    },
    computed:{
        computedLevels(){
            return this.levels;
        }
    },
    methods: {
        getLevels(){
           this.levels = '';
           this.loading = true;
           this.$http.get(`utilities/levels/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.levels = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loading = false;
                   
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loading = false;
           })
        },
        getLevelsNew(response){
            if(response.status == 'success') {
                this.getLevels();
                this.message = 'Level added Successfully';
            } else {
                this.error = response.errors[0]; 
            }
            this.clearMessage();
        },
        launchEditModal(response){
            if(response.status == 'success') {
                this.level = response.data[0];
            } else {
                this.responseErrors = response.errors;
            }
            
        },
        modalMessage(e){
            if(e.status == 'success'){
                this.message = 'This Level updated successfully!';
                this.clearMessage();
            } else {
                this.error = 'Update was not successful';
            }
            this.getLevels();
        },
        deleteLevel(response){
            if(response.status == 'success') {
                this.getLevels();
                this.message = 'This Level deleted Successfully';
                this.clearMessage();
            } else {
                this.responseErrors = 'This Level could not be deleted';
            }
            
        },
        clearMessage(){
            setTimeout(() => {
                this.message = '';
                this.error = '';
                this.responseErrors = '';
            }, 10000);
        }

    },
    created(){
       this.getLevels(); 
    },
    updated(){
        window.jQuery('#datatable').DataTable();
    },
    components: {
        Modal, 
        Loading,
        EditModal,
        SubmitButton,
        LevelRow
    }
}
    
</script>

<style src="@/assets/datatables.css"></style>



