<template>
  <div class="">
    <div class="page-title">
        <div class="title_left">
            <div class="x_content">
                <h3>Student Profile</h3>
            </div>
        </div>
        <div class="title_right">
            <div class="col-md-5 col-sm-5 col-xs-12 pull-right">
                <router-link class="btn btn-round btn-sm btn-primary col-sm-12" to="/student/edit">EDIT PROFILE</router-link>
            </div>
        </div>
    </div>
    <div class="clearfix"></div>

    <div class="row">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <loading v-if="loadingProfile" style="height: 80px"></loading>
        <div class="x_panel">

          <div v-if="studentProfile">
            <table class="table table-striped table-bordered">
              <tbody>
                <tr>
                  <td>First Name</td>
                  <td><strong>{{studentProfile.firstName}}</strong></td>
                </tr>
                <tr>
                  <td>Other Name</td>
                  <td><strong>{{studentProfile.otherName}}</strong></td>
                </tr>
                <tr>
                  <td>Last Name</td>
                  <td><strong>{{studentProfile.lastName}}</strong></td>
                </tr>
                <tr>
                  <td>Class</td>
                  <td><strong>{{studentProfile.class.name}}</strong></td>
                </tr>
                <tr>
                  <td>Reg No</td>
                  <td><strong>{{studentProfile.regNo}}</strong></td>
                </tr>
                <tr>
                  <td>Sex</td>
                  <td><strong>{{studentProfile.sex.name}}</strong></td>
                </tr>
                <tr>
                  <td>Date of Birth</td>
                  <td><strong>{{studentProfile.dateOfBirth}}</strong></td>
                </tr>
                <tr>
                  <td>Contact Address</td>
                  <td><strong>{{studentProfile.contactAddress}}</strong></td>
                </tr>
                <tr>
                  <td>Home Address</td>
                  <td><strong>{{studentProfile.homeAddress}}</strong></td>
                </tr>
                <tr>
                  <td>Home Town</td>
                  <td><strong>{{studentProfile.homeTown}}</strong></td>
                </tr>
                <tr>
                  <td>Religion</td>
                  <td><strong>{{religion}}</strong></td>
                </tr>

              </tbody>
            </table>
          </div>

        </div>
      </div>

    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex';
import Loading from '@/components/utils/loading';
  export default {
    data(){
      return {
        loadingProfile: false,
        responseErrors: '',
        errors: '',
        studentProfile: '',
        message: '',
      }
    },
    computed:{
      ...mapState(['user']),
      religion(){
        if(this.studentProfile.religion == null){
          return '';
        } else {
          return this.studentProfile.religion.name;
        }
      }
    },
    methods: {
      getStudent(){
        this.loadingProfile = true;
        this.$http.get(`students/profile/${this.user.id}`)
        .then(response => {
          if(response.status == 'success'){
            this.studentProfile = response.data
          } else {
            this.responseErrors = this.response.errors;
          }
          this.loadingProfile = false;
        })
        .catch(err => {
            this.errors = err;
            this.loadingProfile = false;
        })
      }
    },
    created(){
      this.getStudent();
    },
    components:{
      Loading
    }
  }
</script>



