<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade downloadExcelModal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Download Result Template</h4>
                    </div>
                    <p v-if="errorMessage" class="alert alert-danger text-center">{{errorMessage}}</p>
                    <!-- <form @submit.prevent="downloadTemplate" data-parsley-validate enctype="multipart/form-data" class="form-horizontal form-label-left" id="form"> -->
                    <div class="modal-body" style="height: 200px;">
                        
                        <loading v-if="loadingTemplate"></loading>
                        <div ref="template">  
                        </div>
                        <!-- <div class="form-group">
                            <label for="term">Term<span class="required"> *</span></label>
                            <select class="form-control btn-round" id="term" v-model="formData.termId">
                                <option value=""><span v-if="loadingTerms"><i class="fa fa-spinner fa-spin"></i> Loading..</span><span v-else>Choose Term</span>  </option>
                                <option v-for="(term, index) in terms" :key="index" :value="term.id">{{term.termType.name}} Term, {{term.session.name}}</option> 
                            </select>
                        </div>

                        
                        <div class="form-group">
                            <label for="klass">Class<span class="required"> *</span></label>
                            <select class="form-control btn-round"  id="klass" v-model="formData.classId">
                                <option value=""><span v-if="loadingClasses"><i class="fa fa-spinner fa-spin"></i> Loading ..</span><span v-else>Choose Class</span> </option>
                                <option v-for="(klass, index) in classes" :key="index" :value="klass.id">{{klass.fullname}}</option> 
                            </select>
                        </div> -->

                        
                        
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                        <!-- <submit-button :loading="loading" class="btn btn-round btn-sm btn-primary">Download</submit-button> -->
                    </div>
                    <!-- </form>  -->
                    
                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>
<script>
import Loading from '@/components/utils/loading';
import SubmitButton from '@/components/utils/submitButton';
    export default {
        props:['initialTerms','initialClasses','loadingTerms','loadingClasses'],
        data(){
            return {
                loading: false,
                errorMessage: '',
                template: '',
                loadingTemplate: false,
                error: '',
                formData:{
                    termId: '',
                    classId: '',
                }
            }
        },
        computed:{
            terms(){
                return this.initialTerms;
            },
            classes(){
                return this.initialClasses;
            }
        },
        methods:{
            downloadTemplate(){
                this.loading = true;
                this.$http.post(`Home/DownloadClassListAction`, this.postValues(),true,true)
                .then(response => {
                    console.log(response);
                    this.loading = false;
                })
                .catch(err => {
                    console.log(err);
                    this.loading = false;
                })
            },
            postValues(){
                let obj = {
                    termId: this.formData.termId,
                    classId: this.formData.classId,

                }
                return obj;
            },
            getTemplate(){
                let params = {}
                this.loadingTemplate = true;
                this.$http.get(`home/downloadclasslist`,params, true)
                .then(response => {
                    this.template = response;
                    console.log(response);
                    this.$refs.template.innerHTML = this.template;
                    this.loadingTemplate = false;
                })
                .catch(err => {
                    // console.log(err);
                    this.loadingTemplate = false;
                })
            }
        },
        created(){
            this.getTemplate();
        },
        components:{
            SubmitButton,
            Loading
        }
    }
</script>


