<template>
    <div>
        <div class="col-sm-12 bg-info border_bottom">
            
            <h4 class="text-center"> 
                <small v-if="!initialAll" class="pull-left">
                    <button class="btn btn-xs btn-info" @click.prevent="all()">All Classes</button>
                </small>
                <b>{{title}}</b>
            </h4>
        </div>
        <div class="clearfix"></div>
        <br>
        
        <table id="datatable" class="table table-striped table-bordered">
            <thead>
                <tr>
                    <th class="text-center">S/N</th>
                    <th class="text-center">Subject</th>

                    <th class="text-center">Class</th>

                    <th class="text-center">Teacher</th>

                    <th class="text-center">Active</th>

                    <th class="text-center">Edit</th>

                    <th class="text-center">Delete</th>
                    <th><input @click="checkAll($event)" type="checkbox" id="checkAll" />
                     <button @click="deleteSelected" :disabled="loadingDeleting" class="btn btn-danger btn-round btn-xs">
                         Delete <i v-if="loadingDeleting" class="fa fa-spin fa-spinner"></i></button> </th>
                </tr>
            </thead>
            <h4 v-if="classSubjects == ''" class="text-center" >No Data available</h4>
            <tbody>
                
                
                <!-- <tr></tr> -->

                <class-subject-row class="classRow" v-for="(classSubject, index) in classSubjects" :key="index" :classSubject="classSubject" :index="index" 
                @deleteClassSubject="deleteClassSubject($event)" @editClassSubject="editClassSubject($event)"></class-subject-row>
            </tbody>
        </table>
    </div>
</template>

<script>
import ClassSubjectRow from '@/components/admin/setting/classSubject/classSubjectRow';
export default {
    props: ['initialClassSubjects', 'initialTitle', 'initialAll'],
    data() {
        return {
            deleteErrors: [],
            deleteResponses: [],
            loadingDeleting: false,
        }
    },
    methods: {
        all(){
            this.$emit('all');
        },
        checkAll(e){
            let checkAll = $('#checkAll').prop('checked');
            let classRow = $('.classRow').find('input[type="checkbox"]');
            if(checkAll == true){
                classRow.each(function(){
                    $(this).prop('checked', true);
                })
            } else {
                classRow.each(function(){
                    $(this).prop('checked', false);
                })
            }
        },
        editClassSubject(response){
            this.$emit('editClassSubject', response);
        },
        deleteSelected(){
            let classRow = $('.classRow').find('input:checked');
            var objthis = this;
            let classRowCount = classRow.length - 1;
            if(confirm('Are you sure, you want to delete these class Subject')){
                this.loadingDeleting = true;
                classRow.each(function(index){
                    let hiddenId = $(this).next('input[type="hidden"]').val();
                    // console.log('This is the index ',index);
                    // console.log(classRowCount);
                    objthis.deleteClassSubj(hiddenId);
                    if(classRowCount == index){
                        objthis.loadingDeleting = false;
                        objthis.$emit('selectDeleteMessage', [objthis.deleteErrors, objthis.deleteResponses]);
                    }
                    
                })
                // this.loadingDeleting = false;
                // this.$emit('selectDeleteMessage', [this.deleteErrors, this.deleteResponses]);
            }
        },
        deleteClassSubj(id){
            this.$http.delete(`settings/classSubject/delete/${id}`)
            .then((response) => {
                // console.log('Response from delete ', response)
                this.deleteResponses.push(response);
            })
            .catch(err => {
                // console.log('Catched Error ', err);
                this.deleteErrors.push(err);
            })

        },
        deleteClassSubject(response){
            this.$emit('deleteClassSubject', response);
        }

    },
    computed:{
        classSubjects(){
            return this.initialClassSubjects;
        },
        title(){
            return this.initialTitle;
        }
    },
    components:{
        ClassSubjectRow
    },
    
    created(){
        // this.getAllComponents();
    }
}
</script>

<style scoped>
    .required{
        color: red;
    }
    .border_bottom{
        border-bottom: 2px solid rgb(190, 189, 189);
    }
</style>


