<template>

    <div>
        <loading v-if="loading"></loading>
        <h3 class="text-center">{{heading}}</h3>
        <table id="datatable" class="table table-striped table-bordered">
            <thead>
                <tr>
                    <th class="text-center">S/N</th>
                    <th class="text-center">Pin</th>

                    <th class="text-center">Serial Number</th>

                </tr>
            </thead>

            <tbody>
                
                <tr></tr>
                <tr v-for="(scratchCard, index) in scratchCards" :key="index">
                    <td class="text-center">{{index + 1}}</td>

                    <td class="text-center">{{scratchCard.pin}}</td>
                    <td class="text-center">{{scratchCard.serialNumber}}</td>
                </tr>
            </tbody>
        </table>
    </div>
    

</template>

<script>
import Loading from '@/components/utils/loading';

    export default {
        props: ['initialScratchCards', 'initialHeading'],
        data(){
            return {
                loading: false,
            }
        },
        computed: {
            scratchCards(){
                return this.initialScratchCards;
            },
            heading(){
                return this.initialHeading;
            }
        },
        components: {
            Loading,
        }
    }
</script>


