<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade edit_modal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">
                    
                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Edit Session</h4>
                    </div>
                    <form @submit.prevent="editSession" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                            <div class="form-group">
                                <label for="name">Name <span class="required">*</span>
                                </label>
                                <input type="text" id="name" v-model="session.name" required class="form-control btn-round">
                            </div>
                         
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="isLoading" class="btn btn-round btn-sm btn-primary">Edit</submit-button>
                    </div>
                    </form> 

                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

export default {
    props:['session'],
    data() {
        return {
            isLoading: false,
            errorMessage: '',
        }
    },
    methods: {
        editSession(){
            this.isLoading = true;
            this.$http.put(`settings/session/update/${this.session.id}`, {name:this.session.name})
            .then(response => {
                this.$emit('response', response);
                this.isLoading = false;
                window.$('.edit_modal').modal('hide');
            })
        },
        

    },
    created() {
        
    },
    components: {
        SubmitButton
    }
}
</script>


