import Vue from 'vue';
import toastr from 'toastr';

import api from './http';
import store from './store';
import router from './router';

const isProduction = process.env.mode === 'production';

const apiRootUrl = isProduction ? 'https://adsms.azurewebsites.net/api/' : 'https://adsms.azurewebsites.net/api/';
const downloadRootUrl = isProduction ? 'https://adsms.azurewebsites.net/' : 'https://adsms.azurewebsites.net/';
// const apiRootUrl = isProduction ? 'https://paylot-api-frontend.herokuapp.com' : 'https://paylot-api-frontend.herokuapp.com';
// const apiRootUrl = 'http://localhost:9100';

const setTokenIfExists = (options) => {

    if (!store.state.token) {
        return;
    }

    if (!options.headers) {
        options.headers = {};
    }

    options.headers.Authorization = `Bearer ${store.state.token}`;
};

toastr.options = {
  closeButton: true,
  debug: true,
  newestOnTop: false,
  progressBar: true,
  positionClass: 'toast-top-right',
  preventDuplicates: false,
  showDuration: '400',
  hideDuration: '1000',
  timeOut: '5000',
  extendedTimeOut: '1000',
  showEasing: 'swing',
  hideEasing: 'linear',
  showMethod: 'fadeIn',
  hideMethod: 'fadeOut',
};
const log = (...message) => {
    if (!isProduction) {
        console.log(...message);
    }
};

const http = {
    request(url, method, data, options = {}, download) {
        let rootUrl;
        if (download === true) {
            rootUrl = downloadRootUrl;
        } else {
            rootUrl = apiRootUrl;
        }
        return new Promise((resolve, reject) => {
            setTokenIfExists(options);
            api({
                method,
                url: rootUrl + url,
                data,
                baseURL: rootUrl,
                ...options,
            }).then((response) => {
                // log('API Success response', response);
                resolve(response.data);
            }).catch((err) => {
                // log('API Call Error', err);
                if (!err.response) {
                    reject('An unexpected error occured. Please, check your internet and try again.');
                } else if (err.response.data.message) {
                    reject(err.response.data.message);
                } else {
                    let message = null;
                    switch (err.response.status) {
                        case 401:
                            message = 'You are not authorized to access this resource.';
                            router.push('/login');
                            break;
                        case 404:
                            message = 'This resource could not be found. Please check the request and try again.';
                            break;
                        case 500:
                            message = `An error occured on the server.
                      Please try again later or contact support if error persists`;
                            break;
                        default:
                            message = 'An unexpected error has occurred. Please, try again.';
                            break;
                    }
                    reject(message);
                }
            });
        });
    },

    get(url, params = {}, download = false) {
        const options = { params };
        return this.request(url, 'GET', null, options, download);
    },

    post(url, data, isJson = true, download = false) {
        const options = {};

        if (!isJson) {
            options.headers = { 'Content-Type': 'multipart/form-data' };

            const formData = new FormData();
            Object.entries(data)
                .forEach(([key, value]) => formData.append(key, value));
            data = formData;
        }

        return this.request(url, 'POST', data, options, download);
    },

    put(url, data, isJson = true) {
        const options = {};

        if (!isJson) {
            options.headers = { 'Content-Type': 'multipart/form-data' };

            const formData = new FormData();
            Object.entries(data)
                .forEach(([key, value]) => formData.append(key, value));
            data = formData;
        }

        return this.request(url, 'PUT', data, options);
    },

    delete(url) {
        return this.request(url, 'DELETE');
    },

    uploadFile(file) {
        const url = `/uploads/${store.state.user._id}`;
        return this.post(url, { file }, false);
    },
};


Object.assign(Vue.prototype, {
    $log: log,
    $http: http,
    $toastr: toastr,
});

export default {
    http,
    log,
    toastr,
};



