<template>
    <tr>
        <td class="text-center">{{index + 1}}</td>
        <td class="text-center">{{term.termType.name}}</td>


        <td class="text-center">{{term.session.name}}</td>

        <td class="text-center"><span v-if="term.isActive">
                <i class="fa fa-check-square-o fa-2x text-success"></i>
            </span>
            <span v-else>
                <i class="fa fa-times-circle fa-2x text-danger"></i>
            </span>
        </td>

        <td class="text-center">
            <submit-button class="btn btn-sm btn-round btn-primary paddingLeftRight" @click="launchEditModal(term.id)" :loading="editLoading">EDIT</submit-button>
        </td>
        <td class="text-center">
            <submit-button class="btn btn-sm btn-round btn-danger paddingLeftRight" :loading="deleteLoading" @click="deleteTerm(term.id)">DELETE</submit-button>
        </td>
        
    </tr>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
import EditModal from '@/components/admin/setting/class/editModal';


    export default {
        props: ['index','term'],
        data() {
            return {
                editLoading: false,
                deleteLoading: false,
                
            }
        },
        methods: {
            launchEditModal(id){
                this.editLoading = true;
                this.$http.get(`utilities/terms/${id}`)
                .then(response => {
                    
                    this.$emit('editTerm', response);
                    window.$('.edit_modal').modal('show');
                    this.editLoading = false;
                })
                .catch(err => {
                    this.$emit('editTerm', err);
                    this.editLoading = false;
                })
            },
            deleteTerm(id){
                 if(confirm('Are you sure, you want to delete this Term')){
                    this.deleteLoading = true;
                    this.$http.delete(`settings/term/delete/${id}`)
                    .then((response) => {
                        this.$emit('deleteTerm', response);
                        this.deleteLoading = false; 
                    })
                    .catch(err => {
                        this.$emit('deleteTerm', err);
                        this.deleteLoading = false;
                    })
                }
            }
        },
        components: {
            SubmitButton,
            EditModal
        }
    }
</script>

<style scoped>
    .paddingLeftRight{
        padding: 2px 20px;
    }
</style>


