<template>
    <tr>
        <td class="text-center">{{index + 1}}</td>
        <td class="text-center">{{classSubject.subjectName}}</td>


        <td class="text-center">{{classSubject.className}}</td>

        <td class="text-center">{{classSubject.staffName}}</td>

        <td class="text-center"><span v-if="classSubject.isActive">
                <i class="fa fa-check-square-o fa-2x text-success"></i>
            </span>
            <span v-else>
                <i class="fa fa-times fa-2x text-danger"></i>
            </span>
        </td>

        <td class="text-center">
            <!-- <submit-button class="btn btn-sm btn-round btn-primary" @click="launchEditModal(classSubjectid)" :loading="editLoading">EDIT</submit-button> -->
            <submit-button class="btn btn-sm btn-round btn-primary" @click="launchEditModal(classSubject.id)" :loading="editLoading">EDIT</submit-button>
            
        </td>
        <td class="text-center">
            <!-- <submit-button class="btn btn-sm btn-round btn-danger" :loading="deleteLoading" @click="deleteClass(classSubject.id)">DELETE</submit-button> -->
            <submit-button class="btn btn-sm btn-round btn-danger" :loading="deleteLoading" @click="deleteClassSubject(classSubject.id)">DELETE</submit-button>
            
        </td>
        <td>
            
            <input class="checkbox" type="checkbox" />
            <input type="hidden" :value="classSubject.id" />
        </td>
        
    </tr>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
import EditModal from '@/components/admin/setting/class/editModal';


    export default {
        props: ['index','classSubject'],
        data() {
            return {
                editLoading: false,
                deleteLoading: false,
                
            }
        },
        methods: {
            launchEditModal(id){
                this.editLoading = true;
                this.$http.get(`utilities/classes/${id}`)
                .then(response => {
                    
                    this.$emit('editClass', response);
                    window.$('.edit_modal').modal('show');
                    this.editLoading = false;
                })
                .catch(err => {
                    this.$emit('editClass', err);
                    this.editLoading = false;
                })
            },
            deleteClassSubject(id){
                 if(confirm('Are you sure, you want to delete this class Subject')){
                    this.deleteLoading = true;
                    this.$http.delete(`settings/classSubject/delete/${id}`)
                    .then((response) => {
                        this.$emit('deleteClassSubject', response);
                        this.deleteLoading = false; 
                    })
                    .catch(err => {
                        this.$emit('deleteClassSubject', err);
                        this.deleteLoading = false;
                    })
                }
            },
            launchEditModal(id){
                this.editLoading = true;
                this.$http.get(`utilities/classSubjects/${id}`)
                .then(response => {
                    this.$emit('editClassSubject', response);
                    window.$('.edit_modal').modal('show');
                    this.editLoading = false;
                })
                .catch(err => {
                    this.$emit('editClassSubject', err);
                    this.editLoading = false;
                })
            }
        },
        // updated(){
        //     window.jQuery('#datatable').DataTable();
        // },
        components: {
            SubmitButton,
            EditModal
        }
    }
</script>


