<template>
  <div class="">
    <div class="page-title">
        <div class="title_left">
            <div class="x_content">
                <h3>Check Previous Result</h3>
            </div>
        </div>
    </div>
    <div class="clearfix"></div>
    <div class="row">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <div class="x_panel">
          <form @submit.prevent="checkResult" data-parsley-validate class="form-horizontal form-label-left">
            <div class="form-group">
                <div class="col-sm-4">
                  <div class="form-group">
                    <label for="session">Session</label>
                    <select class="form-control btn-round" @change="getTerms()" v-model="sessionId" name="session">
                        <option value=""><span v-if="loadingSession">Loading ..</span><span v-else>Choose Session</span></option>
                        <option v-for="(session, index) in sessions" :key="index" :value="session.id">{{session.name}}</option>
                    </select>
                  </div>
                </div>
                <div class="col-sm-4">
                  <div class="form-group">
                    <label for="term">Term</label>
                    <select class="form-control btn-round" v-model="termId" name="term" :disabled="!sessionId">
                        <option value=""><span v-if="loadingTerms">Loading ..</span><span v-else>Choose Term</span></option>
                        <option v-for="(term, index) in terms" :key="index" :value="term.id" >{{term.termType.name}}</option>
                    </select>
                  </div>
                </div>
                <div class="col-sm-4">
                  <div class="form-group">
                    <label for="cardPin">Card Pin</label>
                    <input type="text" name="cardPin" required="required" v-model="cardPin" class="form-control btn-round">
                  </div>
                </div>
                <div class="col-sm-12 text-center">
                  <div class="form-group">
                    <submit-button type="submit" :loading="submitting" class="btn btn-round btn-sm btn-primary col-sm-2 col-sm-offset-5" :disabled="termId == '' || !cardPin">CHECK</submit-button>
                  </div>
                </div>
            </div>
          </form>
        </div>
      </div>
    </div>
    <div class="clearfix"></div>
    <div class="row">
      <div class="col-sm-12 col-md-12">
        <div v-if="message">
            <p class="alert alert-success text-center">{{message}}</p>
        </div>
        <div v-if="errors" class="alert alert-danger text-center">{{errors}}</div>
        <div v-if="responseErrors" class="alert alert-danger text-center">
            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
        </div>
      </div>
    </div>
    <div class="clearfix"></div>
    <div class="row" v-if="result">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <div class="x_panel">

        </div>
      </div>
    </div>

  </div>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
  export default {
    data () {
      return {
        loadingSession: false,
        loadingTerms: false,
        submitting: false,
        sessions: '',
        sessionId: '',
        terms: '',
        termId: '',
        cardPin: '',
        message: '',
        responseErrors: '',
        errors: '',
        result: '',
      }
    },
    methods: {
      getSessions(){
          this.loadingSession = true;
          this.$http.get(`utilities/sessions/`)
          .then(response => {
              if(response){
                  if(response.status == 'success'){
                      this.sessions = response.data;
                  } else {
                      this.responseErrors = response.errors;
                  }
                  this.loadingSession = false;
              }
          })
          .catch(err => {
              errors = err;
              this.loadingSession = false;
          })
      },
      checkResult(){
        this.submitting = true;
        this.$http.get(`results/check?pinNo=${this.cardPin}&termId=${this.termId}`)
        .then(response => {
          if(response.status == 'success'){
            this.result = response.data
          } else {
            this.responseErrors = response.errors;
          }
          this.clearMessage();
          console.log(response);
          this.submitting = false;
        })
        .catch(err => {
          this.errors = err;
          this.submitting = false;
        })
      },
      getTerms(){
            this.terms = '';
            this.loadingTerms = true;
            this.$http.get(`utilities/terms/?sessionId=${this.sessionId}`)
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.terms = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loadingTerms = false;
               }
           })
           .catch(err => {
               this.errors = err;
               this.loadingTerms = false;
           })

        },
        clearMessage(){
          setTimeout(() => {
              this.message = '';
              this.errors = '';
              this.responseErrors = '';
          }, 10000);
        }
    },
    created(){
      this.getSessions();
    },
    components: {
      SubmitButton,
    }
  }
</script>



