import StudentDashboardView from '@/components/student/dashboard/dashboardView';

import StudentDashboard from '@/components/student/dashboard/dashboard';
import StudentProfile from '@/components/student/profile/profile';
import StudentEditProfile from '@/components/student/profile/edit';

import CheckResult from '@/components/student/result/checkResult';
import PreviousResult from '@/components/student/result/previousResult';

export default [
  {
    path: '/student',
    name: 'studentDashboardview',
    component: StudentDashboardView,
    meta: {
      ForStudent: true,
    },
    children: [
      {
        path: 'dashboard',
        name: 'student.dashboard',
        component: StudentDashboard,
      },
      // Profile Routes
      {
        path: 'profile',
        name: 'student.profile',
        component: StudentProfile,
      },
      {
        path: 'edit',
        name: 'student.edit',
        component: StudentEditProfile,
      },
      // Result Routes
      {
        path: 'checkResult',
        name: 'student.checkResult',
        component: CheckResult,
      },
      {
        path: 'previousResult',
        name: 'student.previousResult',
        component: PreviousResult,
      },
    ],
  },
]



