<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade uploadBulkModal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Upload Result</h4>
                    </div>
                    
                    <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    <form @submit.prevent="uploadResult" data-parsley-validate enctype="multipart/form-data" ref="form" class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                       <h4 class="text-danger text-center">Warning!! This overrides all previous results</h4> 
                        <div class="form-group">
                            <label for="term">Term<span class="required"> *</span></label>
                            <select class="form-control btn-round" id="term" v-model="formData.termId">
                                <option value=""><span v-if="loadingTerms"><i class="fa fa-spinner fa-spin"></i> Loading..</span><span v-else>Choose Term</span>  </option>
                                <option v-for="(term, index) in terms" :key="index" :value="term.id">{{term.termType.name}} Term, {{term.session.name}}</option> 
                            </select>
                        </div>

                        
                        <div class="form-group">
                            <label for="klass">Class<span class="required"> *</span></label>
                            <select class="form-control btn-round"  id="klass" @change="getSubjects()" v-model="formData.classId">
                                <option value=""><span v-if="loadingClasses"><i class="fa fa-spinner fa-spin"></i> Loading ..</span><span v-else>Choose Class</span> </option>
                                <option v-for="(klass, index) in classes" :key="index" :value="klass.id">{{klass.fullname}}</option> 
                            </select>
                        </div>

                        <div class="form-group">
                            <label for="subject">Class<span class="required"> *</span></label>
                            <select class="form-control btn-round" :disabled="!formData.classId" id="subject" v-model="formData.subjectId">
                                <option value=""><span v-if="loadingSubjects"><i class="fa fa-spinner fa-spin"></i> Loading ..</span><span v-else>Choose Subject</span> </option>
                                <option v-for="(subject, index) in subjects" :key="index" :value="subject.id">{{subject.subjectName}}</option> 
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="excelFile">Excel File</label>
                            <input type="file" class="form-control btn-round" ref="file" id="excelFile" accept=".xls, .csv, .xlsx" @change="uploadedFile" />
                        </div>
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="loading" :disabled="!formData.classId || !formData.subjectId || !formData.termId" class="btn btn-round btn-sm btn-primary">Upload</submit-button>
                    </div>
                    </form> 
                    
                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>
<script>
import SubmitButton from '@/components/utils/submitButton';
    export default {
        props:['initialTerms','initialClasses','loadingTerms','loadingClasses'],
        data(){
            return {
                loading: false,
                errorMessage: '',
                subjects: '',
                loadingSubjects: '',
                responseErrors: '',
                formData:{
                    termId: '',
                    classId: '',
                    subjectId: '',
                    uploadFile: '',
                }
            }
        },
        computed:{
            terms(){
                return this.initialTerms;
            },
            classes(){
                return this.initialClasses;
            }
        },
        methods:{
            // On change get the subjects for the particular class
            getSubjects(){
                this.formData.subjectId = '';
                this.loadingSubjects = true;
                this.$http.get(`utilities/classSubjects/?classId=${this.formData.classId}`) 
                .then(response => {
                    if(response){
                        if(response.errors == ''){
                            this.subjects = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingSubjects = false;
                    }
                })
                .catch(err => {
                    this.error = '';this.error = err;this.loadingSubjects = false;
                })
            },
            uploadedFile(){
                this.formData.uploadFile = this.$refs.file.files[0];
            },
            uploadResult(){
                this.loading = true;
                let form = new FormData();
                form.append('classId', this.formData.classId);
                form.append('termId', this.formData.termId);
                form.append('classSubjectId', this.formData.subjectId);
                form.append('files', this.formData.uploadFile);
                this.$http.post(`results/multiple`, form)
                .then(response => {
                    console.log('Response form file upload ', response);
                    if(response.status == 'failed'){
                        this.responseErrors = response.errors
                    } else if(response.status == null) {
                        this.responseErrors = 'Something Went wrong';
                    } else {
                        this.$emit('uploadModalMessage', response)
                    }
                    this.loading = false;
                    this.clearMessage();
                })
            },
            clearMessage(){
                setTimeout(() => {
                    this.message = '';
                    this.errors = '';
                    this.responseErrors = '';
                }, 10000);
            },
            assignValues(){
                let form = new FormData();
                let obj = {
                    classId: this.formData.classId,
                    termId: this.formData.termId,
                    classSubjectId: this.formData.subjectId,
                    files: this.formData.uploadFile
                }
                return obj;
            }
        },
        created(){
            
        },
        components:{
            SubmitButton
        }
    }
</script>


