<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade edit_modal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">
                    
                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Edit Class Subject</h4>
                    </div>
                    
                    <form @submit.prevent="updateClassSubject" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                        <div class="form-group">
                            <label for="name">Class 
                            </label>
                            <select class="form-control btn-round" id="level" v-model="classId">
                                <option value=""><span v-if="loadingClasses"><i class="fa fa-spinner"></i> Loading..</span><span v-else>Choose Class</span> </option>
                                <option v-for="(klass, index) in classes" :key="index" :value="klass.id">{{klass.fullname}}</option> 
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="level">Subject </label>
                            {{subjectId}}
                            <select class="form-control btn-round" id="level" v-model="subjectId">
                                <option value=""><span v-if="loadingSubjects"><i class="fa fa-spinner"></i> Loading..</span><span v-else>Choose Subject</span> </option>
                                <option v-for="(subject, index) in subjects" :key="index" :value="subject.id">{{subject.name}}</option> 
                            </select>
                            
                        </div>
                        <div class="form-group">
                            <label for="name">Subject Teacher Name <span class="required">*</span>
                            </label>
                            <input type="text" v-model="classSubject.staffId" id="name" class="form-control btn-round">
                        </div>
                        <div class="form-group">
                            <label>Active</label>
                            <input type="checkbox" v-model="classSubject.isActive" />
                        </div>
                          
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-sm btn-round btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="isLoading" class="btn btn-sm btn-round btn-primary">Edit</submit-button>
                    </div>
                    </form> 

                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

export default {
    props:['initialClassSubject'],
    data() {
        return {
            isLoading: false,
            loadingClasses: false,
            loadingSubjects: false,
            responseErrors: '',
            errorMessage: '',
            error: '',
            subjectId: '',
            classId: '',
            subjects: {},
            classes: {},
        }
    },
    computed:{
        classSubject(){
            return this.initialClassSubject;
        },
        
    },
    methods: {
        getSubjects(){
           this.loadingSubjects = true;
           this.$http.get(`utilities/subjects/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.subjects = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loadingSubjects = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loadingSubjects = false;
           })
        },
        getAllClasses(){
            this.loadingClasses = true;
            this.$http.get(`utilities/classes/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.classes = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loadingClasses = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loadingClasses = false;
           })

        },
        updateClassSubject(){
            this.isLoading = true;
            this.$http.put(`settings/classSubject/update/${this.classSubject.id}`, this.assignValues())
            .then(response => {
                this.$emit('updateResponse', response);
                window.$('.edit_modal').modal('hide');
                this.isLoading = false; 
            })
            .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.isLoading = false;
           })
        },
        assignValues(){
            let obj = {
                classId: this.classId,
                subjectId: this.subjectId,
                isActive: this.classSubject.isActive,
                staffId: this.classSubject.staffId
            }
            return obj;
        },
        
    },
    
    created() {
        this.getSubjects();
        this.getAllClasses();
    },
    watch: {
        classSubject: function(value){
            this.classId = value.classId;
            this.subjectId = value.subjectId
        }
    },
    components: {
        SubmitButton,
    }
}
</script>


