<template>

<!-- sidebar menu -->
    <div id="sidebar-menu" class="main_menu_side hidden-print main_menu">
        <div class="menu_section">
            <h3>General</h3>
            <ul class="nav side-menu">
                <!-- <li><a><i class="fa fa-home"></i> Home <span class="fa fa-chevron-down"></span></a>
                    <ul class="nav child_menu">
                        <li><router-link to="dashboard">Dashboard</router-link></li>
                    </ul>
                </li> -->
                <li v-for="(menu, i) in activeMenus" :key="i">
                    <a v-if="menu.children">
                        <i class="fa" :class="menu.icon || ''"></i>
                        {{menu.label}}
                        <span class="fa fa-chevron-down"></span>
                    </a>

                    <router-link v-else :to="menu.route"><i class="fa" :class="menu.icon"></i>{{menu.label}}</router-link>

                    <ul class="nav child_menu" v-if="menu.children">
                        <li v-for="(child, j) in menu.children" :key="j"><router-link :to="child.route">{{child.label}}</router-link></li>
                    </ul>
                </li>
                <!-- <li><a><i class="fa fa-gear"></i> Setting <span class="fa fa-chevron-down"></span></a>
                    <ul class="nav child_menu">
                        <li><router-link to="/admin/level">Level</router-link></li>
                        <li><router-link to="/admin/class">Class</router-link></li>
                        <li><router-link to="/admin/subject">Subject</router-link></li>
                        <li><router-link to="/admin/classSubject">Class Subject</router-link></li>
                        <li><router-link to="/admin/session">Session</router-link></li>
                        <li><router-link to="/admin/termType">Term Type</router-link></li>
                        <li><router-link to="/admin/term">Term</router-link></li>
                        <li><router-link to="/admin/scratchCard">Scratch Card</router-link></li>
                    </ul>
                </li>
                <li><a><i class="fa fa-graduation-cap"></i> Student <span class="fa fa-chevron-down"></span></a>
                    <ul class="nav child_menu">
                        <li><router-link to="/admin/students">Students List</router-link></li>
                        <li><router-link to="/admin/student/add">Create Student</router-link></li>
                        <li><router-link to="/admin/student/multiple">Create Multiple Students</router-link></li>
                    </ul>
                </li>
                <li><a><i class="fa fa-user"></i> Staff <span class="fa fa-chevron-down"></span></a>
                    <ul class="nav child_menu">
                        <li><router-link to="/admin/staff/add">Create Staff</router-link></li>
                    </ul>
                </li>
                <li><a><i class="fa fa-user"></i> Parent <span class="fa fa-chevron-down"></span></a>
                    <ul class="nav child_menu">
                        <li><router-link to="/admin/parent/message">Send Bulk SMS</router-link></li>
                    </ul>
                </li>
                <li><a><i class="fa fa-file-text-o"></i>Result <span class="fa fa-chevron-down"></span></a>
                    <ul class="nav child_menu">
                        <li><router-link to="/admin/results/add">Add/Upload Results</router-link></li>
                        <li><router-link to="/admin/results/generate">Generate New</router-link></li>
                        <li><router-link to="/admin/results/generated-lists">View Generated</router-link></li>
                    </ul>
                </li> -->
            </ul>
        </div>


    </div>
<!-- /sidebar menu -->

</template>

<script>
import { mapState } from 'vuex';
import AdminMenus from '@/sideBarAdminMenus';

import StudentMenus from '@/sideBarStudentMenus';

export default {
    data(){
        return {
            menus: {
                admin: AdminMenus,
                student: StudentMenus,
                // parent: parentMenus,
                user: [],
                parent: [],
                staff: [],
            }
        }
    },
    methods: {
    },
    computed:{
        activeMenus(){
          let userRole = this.user.role;
          if(userRole == 'Admin'){
            return this.menus['admin'];
          } else if(userRole == 'Student'){
            return this.menus['student'];
          } else {
            return this.menus['admin'];
          }
        },
        ...mapState(['user'])
    },
    created(){

    }
}
</script>



