<template>
  <div class="">

    <div class="page-title">
        <div class="title_left">
            <div class="x_content">
                <h3>Check Current Result</h3>
            </div>
        </div>
    </div>
    <div class="clearfix"></div>
    <div class="row">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <div class="x_panel">
          <form @submit.prevent="checkResult" data-parsley-validate class="form-horizontal form-label-left">
            <div class="col-sm-10 col-sm-offset-1">
                <div class="form-group">
                    <h4 for="first-name" class="text-center">Card Pin</h4>
                    <input type="text" id="first-name" required="required" v-model="cardPin" class="form-control col-md-7 col-xs-12 btn-round">
                </div>
                <div class="form-group">
                    <submit-button :loading="submitting" :disabled="!cardPin" class="btn btn-round btn-sm btn-primary col-sm-4 col-sm-offset-4">CHECK</submit-button>
                </div>
            </div>

          </form>
        </div>
      </div>
      <div class="clearfix"></div>
      <div class="row">
        <div class="col-sm-12 col-md-12">
          <div v-if="message">
              <p class="alert alert-success text-center">{{message}}</p>
          </div>
          <div v-if="errors" class="alert alert-danger text-center">{{errors}}</div>
          <div v-if="responseErrors" class="alert alert-danger text-center">
              <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
          </div>
        </div>
      </div>

      <div class="clearfix"></div>
      <div class="row" v-if="result">
        <div class="col-md-12 col-sm-12 col-xs-12">
          <div class="x_panel">

          </div>
        </div>
      </div>

    </div>

  </div>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
  export default {
    data() {
      return {
        submitting: false,
        cardPin: '',
        result: '',
        message: '',
        responseErrors: '',
        errors: '',

      }
    },
    methods: {
      checkResult(){
        this.submitting = true;
        this.$http.get(`results/check?pinNo=${this.cardPin}`)
        .then(response => {
          if(response.status == 'success'){
            this.result = response.data
          } else {
            this.responseErrors = response.errors;
          }
          this.clearMessage();
          console.log(response);
          this.submitting = false;
        })
        .catch(err => {
          this.errors = err;
          this.submitting = false;
        })
      },
      clearMessage(){
          setTimeout(() => {
              this.message = '';
              this.errors = '';
              this.responseErrors = '';
          }, 10000);
      }
    },
    components:{
      SubmitButton
    }
  }
</script>



