<template>
    
    <div class="">
        <div class="page-title">
            <div class="title_left">
               
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" data-toggle="modal" data-target=".bs-example-modal-lg">ADD NEW CLASS</button>
                    <modal @modalMessage="getClassesNew($event)"></modal>
                </div>
                
            </div>
            <div class="title_right">
                <div class="col-md-5 col-sm-5 col-xs-12 form-group pull-right top_search">
                    <div class="input-group">
                    <input type="text" class="form-control" placeholder="Search for..." />

                    <span class="input-group-btn">
                        <button class="btn btn-default" type="button">Go!</button>
                    </span>
                    </div>
                </div>
            </div>
        </div>

        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                
                <div class="x_panel">
                    <div class="x_title">
                        <h2>
                            Classes
                        </h2>

                        <ul class="nav navbar-right panel_toolbox">
                            <li>
                                <a class="collapse-link">
                                <i class="fa fa-chevron-up"></i>
                                </a>
                            </li>

                        </ul>

                        <div class="clearfix"></div>
                    </div>
                    <div class="x_content">
                        <div v-if="message">
                            <p class="alert alert-success text-center">{{message}}</p>
                        </div>
                        <p v-if="error" class="alert alert-danger text-center">{{error}}</p>
                        <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    </div>
                    <div class="x_panel">
                        <div v-if="loading" class="col-sm-12" style="min-height: 60px;">
                            <loading style="height:50px;"></loading>
                        </div>
                        <div class="x_content" v-if="classes">
                        <table id="datatable" class="table table-striped table-bordered">
                            <thead>
                                <tr>
                                    <th class="text-center">S/N</th>
                                    <th class="text-center">Name</th>

                                    <th class="text-center">Level</th>

                                    <th class="text-center">Form Teacher</th>

                                    <th class="text-center">Active</th>

                                    <th class="text-center">Edit</th>

                                    <th class="text-center">Delete</th>
                                </tr>
                            </thead>

                            <tbody>
                                
                                <!-- <tr></tr> -->
                                <class-row v-for="(klass, index) in classes" :key="index" :klass="klass" :index="index" @editClass="launchEditModal($event)" @deleteClass="deleteClass($event)"></class-row>
                            </tbody>
                        </table>
                    </div>
                    </div>
                    
                </div>
            </div>
        </div>
        
        <edit-modal :initialKlass="klass" :initialLevelId="levelId" @updateResponse="updateMessage($event)"></edit-modal>
        
        
    </div>

    
</template>
<script>
import ClassRow from '@/components/admin/setting/class/classRow';
import EditModal from '@/components/admin/setting/class/editModal';
import Loading from '@/components/utils/loading';
import Modal from '@/components/admin/setting/class/modal';

export default {
    data () {
        return {
            loading: false,
            responseErrors: '',
            error: '',
            classes: {},
            klass: '',
            levelId: '',
            message: '',
        }
    },
    methods: {
        getAllClasses(){
            this.classes = '';
            this.loading = true;
            this.$http.get(`utilities/classes/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.classes = response.data;
                   } else {
                       this.responseError = response.errors;
                   }
                   this.loading = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loading = false;
           })

        },
        getClassesNew(response){
            if(response.status == 'success') {
                this.getAllClasses();
                this.message = 'Class added Successfully';
            } else {
                this.error = response.errors[0]; 
            }
            this.clearMessage();
        },
        launchEditModal(response){
            
            if(response.status == 'success') {
                this.klass = response.data[0];
                this.levelId = this.klass.level.id;
            } else {
                this.responseError = response.errors;
            }
            
        },
        updateMessage(response){
            if(response.status == 'success'){
                this.message = 'This Class updated successfully!';
                this.clearMessage();
            } else {
                this.error = 'Update was not successful';
            }
            this.getAllClasses();
        },
        deleteClass(response){
            if(response.status == 'success') {
                this.getAllClasses();
                this.message = 'This Class deleted Successfully';
                this.clearMessage();
            } else {
                this.responseErrors = response.errors;
                this.clearMessage();
            }
        },
        clearMessage(){
            setTimeout(() => {
                this.message = '';
                this.error = '';
                this.responseErrors = '';
            }, 10000);
        }
    },
    created(){
        this.getAllClasses();
    },
    updated(){
        window.jQuery('#datatable').DataTable();
    },
    components: {
        Modal,
        Loading,
        ClassRow,
        EditModal
    }
}
    
</script>

<style src="@/assets/datatables.css"></style>



