<template>
  <button
    :type="type"
    class="btn"
    @click="onClick"
    :disabled="loading || disabled">
    <slot/>
    <i class="fa fa-spinner fa-spin" v-if="loading"/>
  </button>
</template>

<script>
export default {
  props: {
    loading: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: 'submit',
    },
  },
  methods: {
    onClick() {
      this.$emit('click');
    },
  },
};
</script>



