<template>
    <tr>
        <td class="text-center">{{index + 1}}</td>
        <td class="text-center">{{result.className}}</td>
        <td class="text-center">{{result.termName}}</td>
        <td class="text-center">
            <button class="btn btn-round btn-success" :disabled="loading" @click="regenerateResult" >{{buttonText}}</button>
        </td>
        <td class="text-center">
            <a class="btn-round btn btn-danger" :href="result.downloadUrl" target="_blank">DOWNLOAD</a>
        </td>
    </tr>
</template>

<script>
export default {
    props:['index','initialResult'],
    data(){
        return {
            loading: false,
            responseErrors: '',
            errors: [],
            buttonText: 'REGENERATE',
        }
    },
    computed:{
        result(){
            return this.initialResult;
        }
    },
    methods: {
        regenerateResult(){
            this.loading = true;
            this.buttonText = 'GENERATING ..';
            this.$http.get(`results/generate?classId=${this.result.classId}&termId=${this.result.termId}`)
            .then(response => {
                console.log(response);
                if(response.status == 'success'){
                    this.compileForDownload();
                }
            })
            .catch(err => {
                this.errors = err;this.loading = false;this.buttonText = 'REGENERATE';
            })
        },
        compileForDownload(){
            this.buttonText = 'COMPILING ..'
            this.$http.get(`results/download?classId=${this.result.classId}&termId=${this.result.termId}`)
            .then(response => {
                this.compilingResult = false;
                if(response.status == 'success'){
                    
                } else {
                    this.responseErrors = response.errors;
                }
                this.buttonText = 'REGENERATE';
                this.loading = false;
            })
            .catch(err => {
                this.errors.push(err);this.loading = false;this.buttonText = 'REGENERATE';
            })
        }
    }
}

</script>


