<template>
    <div class="">
        <div class="page-title">
            <div class="title_left">
                <h3>Create Student</h3>
            </div>
            <div class="title_right">
                <div class="col-md-5 col-sm-5 col-xs-12 pull-right">
                    <router-link class="btn btn-round btn-sm btn-primary col-sm-12" to="/students">Students List</router-link>
                </div>
            </div>
        </div>
        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>Create New Student Profile</h2>
                        <ul class="nav navbar-right panel_toolbox">
                            <li><a class="collapse-link"><i class="fa fa-chevron-up"></i></a></li>
                        </ul>
                        <div class="clearfix"></div>
                        
                    </div>
                    <div class="x_content">
                        <div v-if="message">
                            <p class="alert alert-success text-center">{{message}}</p>
                        </div>
                        <p v-if="errors" class="alert alert-danger text-center">{{errors}}</p>
                        <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    </div>

                    <div class="x_content">

                        <i>Please, fill all compulsory fields marked <span class="required">*</span></i>
                        <hr>
                        <h3><strong>Personal Details</strong></h3>
                        <hr>
                        <form @submit.prevent="createStudent" data-parsley-validate class="form-horizontal form-label-left">
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="first-name">First Name <span class="required">*</span></label>
                                    <input type="text" id="first-name" required="required" v-model="formData.firstName" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="last-name">Last Name <span class="required">*</span></label>
                                    <input type="text" id="last-name" v-model="formData.lastName" required="required" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="middle-name">Other Name</label>
                                    <input id="middle-name" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="formData.otherName">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="email">Email</label>
                                    <input id="email" class="form-control col-md-7 col-xs-12 btn-round" type="email" v-model="formData.email">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="telephone">Telephone</label>
                                    <input id="telephone" class="form-control col-md-7 col-xs-12 btn-round" type="tel" v-model="formData.telephone">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="sex">Sex <span class="required">*</span></label>
                                    <select id="sex" class="btn-round form-control" v-model="formData.sexId">
                                        <option value=""><span v-if="loadingSexes"> Loading ..</span><span v-else>Choose Sex</span>  </option>
                                        <option v-for="(sex, index) in sexes" :value="sex.id" :key="index">{{sex.name}}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="religion">Religion</label>
                                    <select id="religion" class="btn-round form-control" v-model="formData.religionId">
                                        <option value=""><span v-if="loadingReligions"> Loading..</span><span v-else>Choose Religion</span>  </option>
                                        <option v-for="(religion, index) in religions" :key="index" :value="religion.id">{{religion.name}}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="dob">Date of Birth <span class="required">*</span></label>
                                    <input id="dob" required class="form-control col-md-7 col-xs-12 btn-round date-picker" type="date" v-model="formData.dateOfBirth">
                                </div>
                            </div>
                            <div class="clearfix"></div>
                            <hr>
                                <h3><strong>Contact Details</strong></h3>
                            <hr>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="state">State </label>
                                    <select id="state" class="btn-round form-control" v-model="formData.stateId">
                                        <option value=""><span v-if="loadingStates"> Loading ..</span><span v-else>Choose State</span>  </option>
                                        <option v-for="(studentState, index) in states" :key="index">{{studentState.name}}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="lga">LGA</label>
                                    <select id="lga" class="btn-round form-control" v-model="formData.lgaId">
                                        <option value="">Choose LGA</option>
                                        <option>Christian</option>
                                        <option>Muslim</option>
                                        <option>Aetheist</option>
                                        <option>Buddist</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="contactAd">Contact Address <span class="required">*</span></label>
                                    <input id="contactAd" required class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="formData.contactAddress">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="permanentAd">Permanent Home Address</label>
                                    <input id="permanentAd" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="formData.homeAddress">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="homeTown">Home Town</label>
                                    <input id="homeTown" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="formData.homeTown">
                                </div>
                            </div>
                            <div class="clearfix"></div>
                            <hr>
                                <h3><strong>Parent/Guardian Details</strong></h3>
                            <hr>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="parentFullName">Full Name <span class="required">*</span></label>
                                    <input type="text" id="parentFullName" required="required" v-model="formData.parent.fullName" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="parentPhone">Parent/Guardian Phone <span class="required">*</span></label>
                                    <input id="parentPhone" placeholder="08063999999" required class="form-control col-md-7 col-xs-12 btn-round" type="tel" v-model="formData.parent.phone">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="parentEmail">Parent/Guardian Email <span class="required">*</span></label>
                                    <input id="parentEmail" required class="form-control col-md-7 col-xs-12 btn-round" type="email" v-model="formData.parent.email">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="relationship">Relationship <span class="required">*</span></label>
                                    <select id="relationship" class="btn-round form-control" v-model="formData.parent.relationship">
                                        <option value="">Select</option>
                                        <option :value="'Father'">Father</option>
                                        <option :value="'Mother'">Mother</option>
                                        <option :value="'Brother'">Brother</option>
                                        <option :value="'Sister'">Sister</option>
                                        <option :value="'Uncle'">Uncle</option>
                                        <option :value="'Aunt'">Aunt</option>
                                        <option :value="'Benefactor'">Benefactor</option>
                                    </select>
                                </div>
                            </div>
                            <div class="clearfix"></div>
                            <hr>
                                <h3><strong>Other Details</strong></h3>
                            <hr>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="studentClass">Class <span class="required">*</span></label>
                                    <select id="studentClass" required class="btn-round form-control" v-model="formData.classId">
                                        <option value=""><span v-if="loadingClasses"> Loading ..</span><span v-else>Select Class</span></option>
                                        <option v-for="(studentClass, index) in classes" :key="index" :value="studentClass.id">{{studentClass.fullname}}</option>
                                    </select>
                                    
                                </div>
                            </div>
                            <br><br>
                            <div class="col-sm-12">
                                <br>
                                <div class="form-group">
                                    <button :disabled="checkFields || submitting" class="btn btn-round btn-info col-sm-2">SUBMIT <i v-if="submitting" class="fa fa-spin fa-spinner"></i></button>
                                </div>
                                
                            </div>
                        </form>
                    </div>
                </div>

                

            </div>
        </div>

    </div>
</template>

<script>
    export default {
        data(){
            return {
                loadingClasses: false,
                loadingReligions: false,
                loadingSexes: false,
                loadingStates: false,
                loadinglgas: false,
                submitting: false,
                message: '',
                responseErrors: '',
                religions: '',
                sexes: '',
                states: '',
                lgas: '',
                errors: '',
                classes: '',
                formData: {
                    firstName: '',
                    lastName: '',
                    otherName: '',
                    sexId: '',
                    telephone: '',
                    religionId: '',
                    dateOfBirth: '',
                    stateId: '',
                    lgaId: '',
                    contactAddress: '',
                    homeAddress: '',
                    homeTown: '',
                    classId: '',
                    parent: {
                        phone: '',
                        email: '',
                        fullName: '',
                        relationship: '',
                    },
                    
                }
            }
        },
        computed:{
            checkFields(){
                if(this.formData.firstName == ''){
                    return true;
                } 
                if(this.formData.lastName == ''){
                    return true;
                }
                if(this.formData.sexId == ''){
                    return true;
                }
                if(this.formData.dateOfBirth == ''){
                    return true;
                }
                if(this.formData.contactAddress == ''){
                    return true;
                }
                if(this.formData.parent.fullname == ''){
                    return true;
                }
                if(this.formData.parent.phone == ''){
                    return true;
                }
                if(this.formData.parent.relationship == ''){
                    return true;
                }
                if(this.formData.parent.email == ''){
                    return true;
                }
                if(this.formData.classId == ''){
                    return true;
                }
                return false;
            }
        },
        methods:{
            getAllClasses(){
                this.loadingClasses = true;
                this.$http.get(`utilities/classes/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.classes = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingClasses = false;
                    }
                })
                .catch(err => {
                    this.error = '';this.errors = err;this.loadingClasses = false;
                })

            },
            getReligion(){
                this.loadingReligions = true;
                this.$http.get(`utilities/religions`)
                .then(response => {
                    if(response.status == 'success'){
                        this.religions = response.data;
                    }
                    this.loadingReligions = false;
                })
            },
            getSex(){
                this.loadingSexes = true;
                this.$http.get(`utilities/sexes`)
                .then(response => {
                    if(response.status == 'success'){
                        this.sexes = response.data;
                    }
                    this.loadingSexes = false;
                })
            },
            getStates(){
                this.loadingStates = true;
                this.$http.get(`utilities/states`)
                .then(response => {
                    if(response.status == 'success'){
                        this.states = response.data;
                    }
                    this.loadingStates = false;
                })
            },
            
            getLgas(){

            },
            createStudent(){
                this.submitting = true;
                this.$http.post(`students`, this.assignValues())
                .then(response => {
                    if(response.status == 'success'){
                        this.message = 'Student was created successfully!!';
                    } else {
                        this.responseErrors = response.errors;
                    }
                    console.log(response);
                    this.clearMessage()
                    this.submitting = false;
                })
                .catch(err => {
                    this.errors = err; this.submitting = false;this.clearMessage()
                })
            },
            assignValues(){
                let obj = {
                    firstName: this.formData.firstName,
                    lastName: this.formData.lastName,
                    otherName: this.formData.otherName,
                    sexId: this.formData.sexId,
                    telephone: this.formData.telephone,
                    religionId: this.formData.religionId,
                    dateOfBirth: this.formData.dateOfBirth,
                    stateId: this.formData.stateId,
                    lgaId: this.formData.lgaId,
                    contactAddress: this.formData.contactAddress,
                    homeAddress: this.formData.homeAddress,
                    homeTown: this.formData.homeTown,
                    classId: this.formData.classId,
                    parent: {
                        phone: this.formData.parent.phone,
                        email: this.formData.parent.email,
                        fullName: this.formData.parent.fullName,
                        relationship: this.formData.parent.relationship,
                    },
                }
                console.log(obj);
                return obj;
                // return JSON.stringify(Object.assign({}, this.formData));
            },
            clearMessage(){
                setTimeout(() => {
                    this.message = '';
                    this.errors = '';
                    this.responseErrors = '';
                }, 10000);
            }
        },
        created(){
            this.getAllClasses();
            this.getReligion();
            this.getSex();
            this.getStates();
        }
    }
</script>
<style>
    .required{
        color:red !important;
    }
</style>


