<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade messageByClassModal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Send SMS by Class</h4>
                    </div>
                    
                    <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    <form @submit.prevent="sendMessage" data-parsley-validate enctype="multipart/form-data" ref="form" class="form-horizontal form-label-left">
                    <div class="modal-body"> 


                        <h4>Recipients/Parents</h4>

                        <div class="form-group">
                            <label for="klass">Class<span class="required"> *</span></label>
                            <select class="form-control btn-round"  id="klass" v-model="classId">
                                <option value=""><span v-if="loadingClasses"><i class="fa fa-spinner fa-spin"></i> Loading ..</span><span v-else>Choose Class</span> </option>
                                <option v-for="(klass, index) in classes" :key="index" :value="klass.id">{{klass.fullname}}</option> 
                            </select>
                        </div>


                        <div class="form-group">
                            <label for="message">Message<span class="required"> *</span></label>
                            <textarea class="form-control btn-round" rows="10" v-model="message"></textarea>
                        </div>
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="loading" :disabled="!classId || !message" class="btn btn-round btn-sm btn-primary">Send Message</submit-button>
                    </div>
                    </form> 
                    
                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
    export default {
        data(){
            return {
                loadingClasses: false,
                classId: '',
                message: '',
                loading: false,
                classes: '',
                responseErrors: '',
            }
        },
        methods: {
            getAllClasses(){
                this.loadingClasses = true;
                this.$http.get(`utilities/classes/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.classes = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingClasses = false;
                    }
                })
                .catch(err => {
                    this.error = '';this.errors = err;this.loadingClasses = false;
                })
            },
            sendMessage(){

            }
        },
        created(){
            this.getAllClasses()
        },
        components:{
            SubmitButton
        }
    }
</script>


