<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade edit_modal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Edit Class</h4>
                    </div>
                    
                    <form @submit.prevent="updateClass" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                        <div class="form-group">
                            <label for="name">Name <span class="required">*</span>
                            </label>
                            <input type="text" id="name" v-model="klass.name" required class="form-control btn-round">
                        </div>
                        <div class="form-group">
                            <label for="level">Level <span class="required">*</span></label>
                            
                            <select class="form-control btn-round" id="level" v-model="levelId">
                                <option value=""><span v-if="loadingLevel"><i class="fa fa-spinner"></i> Loading..</span><span v-else>Choose Level</span> </option>
                                <option v-for="(level, index) in levels" :key="index" :value="level.id">{{level.name}}</option> 
                            </select>
                            
                        </div>
                        <div class="form-group">
                            <label for="name">Form Teacher Name <span class="required">*</span>
                            </label>
                            <input type="text" v-model="klass.formTeacherId" id="name" class="form-control btn-round">
                        </div>
                        <div class="form-group">
                            <label>Active</label>
                            <input type="checkbox" v-model="klass.isActive" />
                        </div>
                          
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-sm btn-round btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="isLoading" class="btn btn-sm btn-round btn-primary">Edit</submit-button>
                    </div>
                    </form> 

                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

export default {
    props:['initialKlass','initialLevelId'],
    data() {
        return {
            isLoading: false,
            loadingLevel: false,
            errorMessage: '',
            levels: {},
            levelId: '',
        }
    },
    computed:{
        klass(){
            return this.initialKlass;
        },
        
    },
    methods: {
        getLevels(){
           this.loadingLevel = true;
           this.$http.get(`utilities/levels/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.levels = response.data;
                   } else {
                       this.responseErrorLevel = response.errors;
                   }
                   this.loadingLevel = false;
               }
           })
           .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.loadingLevel = false;
           })
        },
        updateClass(){
            this.isLoading = true;
            this.$http.put(`settings/class/update/${this.klass.id}`, this.assignValues())
            .then(response => {
                this.$emit('updateResponse', response);
                window.$('.edit_modal').modal('hide');
                this.isLoading = false; 
            })
            .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.isLoading = false;
           })
        },
        assignValues(){
            let obj = {
                name: this.klass.name,
                level: {
                    id: this.levelId
                },
                isActive: this.klass.isActive,
                formTeacherId: this.klass.formTeacherId
            }
            return obj;
        },
        
    },
    
    created() {
        this.getLevels();
    },
    watch: {
        initialLevelId: function(oldValue){
            this.levelId = oldValue;
        }
    },
    components: {
        SubmitButton,
    }
}
</script>


