<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade card_session" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Generate Sessional Scratch Card</h4>
                    </div>
                    <p v-if="errorMessage" class="alert alert-danger">{{errorMessage}}</p>
                    <form @submit.prevent="submit" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                        <div class="form-group">
                            <label for="name">Session <span class="required">*</span>
                            </label>
                            <select class="form-control btn-round" v-model="formData.sessionId" id="level">
                                <option value="">Choose Session </option>
                                <option v-for="(session, index) in sessions" :key="index" :value="session.id"> {{session.name}}</option> 
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="count">Number Of Scratch cards <span class="required">*</span>
                            </label>
                            <input type="number" id="count" v-model="formData.count" required class="form-control btn-round">
                        </div>
                         
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-sm btn-round btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="isLoading" class="btn btn-round btn-sm btn-primary">Submit</submit-button>
                    </div>
                    </form> 

                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

export default {
    props:['initialSessions'],
    data() {
        return {
            isLoading: false,
            errorMessage: '',
            formData: {
                sessionId: '',
                count: ''
            }
        }
    },
    computed: {
        sessions(){
            return this.initialSessions;
        }
    },
    methods: {
        submit(){
            this.isLoading = true;
            this.$http.post('scratch_cards/session/add', this.assignValues())
            .then((response) => {
                if(response) {
                   this.$emit('sessionModalMessage', response);
                   window.$('.card_session').modal('hide'); 
                   this.isLoading = false;
                }
            })
            .catch(err => {
                this.errorMessage = err;
                this.isLoading = false;
            })
        },
        assignValues(){
            let obj = {
                sessionId: this.formData.sessionId,
                count: this.formData.count
            }
            return obj;
        }
        
    },
    
    components: {
        SubmitButton
    }
}
</script>


