<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Add New Class Subject</h4>
                    </div>
                    
                    <p v-if="errorMessage" class="alert alert-danger text-center">{{errorMessage}}</p>
                    <form data-parsley-validate class="form-horizontal form-label-left" @submit.prevent="addData">
                        <div class="modal-body">
                            
                            <div class="form-group">
                                <label for="level">Level <span class="required">*</span></label>
                                
                                <select class="form-control btn-round" v-model="level" @change="onChangeLevel()" id="level">
                                    <option value=""><span v-if="loadingLevel"><i class="fa fa-spinner"></i> Loading..</span><span v-else>All Levels</span> </option>
                                    <option v-for="(level, index) in levels" :key="index" :value="level.id">{{level.name}}</option> 
                                </select>
                            </div>

                            <div class="form-group">
                                <label for="level">Select Class(es) <span class="required">*</span></label>
                                
                                <multiselect v-model="formData.levelClass" :options="optionsLevelClass" :loading="loadingLevelClass" :disabled="loadingLevelClass" required :closeOnSelect="false"
                                :multiple="true" group-values="levelClasses" group-label="selectLevelClass" :group-select="true" :placeholder="placeholder" track-by="id" label="fullname">
                                    <span slot="noResult">Oops! No elements found. Consider changing the search query.</span>
                                </multiselect>
                            </div>
                            
                            <div class="form-group">
                                
                                <label class="typo__label">Select Subject(s) <span class="required">*</span></label>
                                <multiselect v-model="formData.subject" :options="options" :multiple="true" :loading="loadingSubject" :disabled="loadingSubject" required :closeOnSelect="false"
                                group-values="subjects" group-label="selectAll" :group-select="true" :placeholder="placeholderSubject" track-by="id" label="name">
                                    <span slot="noResult">Oops! No elements found. Consider changing the search query.</span>
                                </multiselect>
                            </div>

                            <div class="form-group">
                                <label for="name">Form Teacher Name
                                </label>
                                <input type="text" v-model="formData.staffId" id="name" class="form-control btn-round">
                            </div>
                            
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                            <submit-button :loading="loading" class="btn btn-round btn-sm btn-primary">Submit</submit-button>
                        </div>
                    </form> 
                    
                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

import Multiselect from 'vue-multiselect';

export default {
    props:['initialLevels'],
    data() {
        return {
            loadingLevel: false,
            loading: false,
            loadingSubject: false,
            loadingLevelClass: false,
            name: '',
            responseErrorLevel: '',
            errorMessage: '',
            options: [
                {
                    selectAll: 'Select All',
                    subjects: [],
                }
            ],
            optionsLevelClass: [
                {
                    selectLevelClass: 'Select All',
                    levelClasses: [],
                }
            ],
            subject: '',
            level: '',
            levelClass: '',
            placeholder: '',
            placeholderSubject: '',
            formData: {
                levelClass: '',
                subject: '',
                staffId: '',
            }
        }
    },
    computed:{
        levels(){
            return this.initialLevels;
        },
        classSubjects(){
            return this.initialClassSubjects;
        }
    },
    methods: {
        
        getSubjects(){
           this.loadingSubject = true;
           this.placeholderSubject = 'loading ..';
           this.$http.get(`utilities/subjects/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.options[0].subjects = response.data;
                   } else {
                       this.responseError = response.errors;
                   }
                   this.loadingSubject = false;
                   this.placeholderSubject = 'Type to search';
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loadingSubject = false;this.placeholderSubject = 'Type to search';
           })
        },
        getAllClasses(){
            this.formData.levelClass = '';
            this.loadingLevelClass = true;
            this.placeholder = 'loading ..';
            this.$http.get(`utilities/classes/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.optionsLevelClass[0].levelClasses = response.data;
                   } else {
                       this.responseError = response.errors;
                   }
                   this.loadingLevelClass = false;
                   this.placeholder = 'Type to search';
               }
           })
           .catch(err => {
               this.error = '';this.error = err;this.loadingLevelClass = false;this.placeholder = 'Type to search';
           })
        },
        onChangeLevel(){
            if(this.level == ''){
                this.getAllClasses();
            } else {
                this.getLevelClass();
            }
        },
        //On change get the class
        getLevelClass(){
            this.formData.levelClass = '';
            this.loadingLevelClass = true;
            this.placeholder = 'loading ..';
            this.$http.get(`utilities/classes/?levelId=${this.level}`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.optionsLevelClass[0].levelClasses = response.data;
                   } else {
                       this.responseError = response.errors;
                   }
                   this.loadingLevelClass = false;this.placeholder = 'Type to search';
               }
           })
           .catch(err => {
               this.error = '';this.error = err;this.loadingLevelClass = false;this.placeholder = 'Type to search';
           })
        },
        addData(){
            this.loading = true;
            this.$http.post('settings/classSubject/add', this.assignValues())
            .then((response) => {
                if(response) {
                   this.$emit('modalMessage', response);
                   window.$('.bs-example-modal-lg').modal('hide'); 
                   this.loading = false;
                }
            })
            .catch(err => {
                this.errorMessage = err;
                this.loading = false;
            })
        },
        assignValues(){
            let obj = {
                classIds: this.formData.levelClass.map(obj => obj.id),
                subjectIds: this.formData.subject.map(obj => obj.id),
                staffId: this.formData.staffId,
            }
            return obj;
        }
    },

    created(){
        this.getSubjects();
        this.getAllClasses();
    },
    components: {
        SubmitButton,
        Multiselect
    }
}
</script>

<style>
    @import 'vue-multiselect/dist/vue-multiselect.min.css';
    .multiselect__tags{
        border-radius:20px;border-color: rgb(185, 184, 184);
    }
    .multiselect__single{
        color: rgb(158, 157, 157); font-size: 1em;
    }
    .multiselect__tag{
        background: rgb(1, 111, 145);
    }
    .multiselect__tag-icon:after{
        color: rgb(190, 3, 3);
    }
    .multiselect__tag-icon:hover{
        background: rgb(2, 50, 105);
    }
    .form-group{
        margin-bottom: 16px;
    }
</style>


