<template>
    
    <div class="">
        <div class="page-title">
            <div class="title_left">
               
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" data-toggle="modal" data-target=".bs-example-modal-lg">ADD NEW CLASS SUBJECT</button>
                    <modal :initialLevels="levels" @modalMessage="getClassSubjectsNew($event)"></modal>
                    
                    <!-- <modal @modalMessage="getClassesNew($event)"></modal> -->
                </div>
                
            </div>
            <div class="title_right">
                <div class="col-md-5 col-sm-5 col-xs-12 form-group pull-right top_search">
                    <div class="input-group">
                    <input type="text" class="form-control" placeholder="Search for..." />

                    <span class="input-group-btn">
                        <button class="btn btn-default" type="button">Go!</button>
                    </span>
                    </div>
                </div>
            </div>
        </div>

        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                
                <div class="x_panel">
                    <div class="x_title">
                        <h2>
                            Class Subjects
                        </h2>

                        <ul class="nav navbar-right panel_toolbox">
                            <li>
                                <a class="collapse-link">
                                <i class="fa fa-chevron-up"></i>
                                </a>
                            </li>

                        </ul>

                        <div class="clearfix"></div>
                    </div>
                    <div class="x_content">
                        <div v-if="message">
                            <p class="alert alert-success text-center">{{message}}</p>
                        </div>
                        <p v-if="error" class="alert alert-danger text-center">{{error}}</p>
                        <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    </div>
                    
                    <div class="x_content">
                        <form @submit.prevent="getSpecificClassSubject()" class="form-horizontal form-label-left">
                            <div class="form-group">
                                <div class="col-sm-5">
                                    <select class="form-control btn-round" @change="getLevelClass()" v-model="level">
                                        <option value=""><span v-if="loadingLevel"><i class="fa fa-spinner"></i> Loading ..</span><span v-else>Choose Level</span></option>
                                        <option v-for="(level, index) in levels" :key="index" :value="level.id">{{level.name}}</option>
                                    </select>
                                </div>
                                <div class="col-sm-5">
                                    <select class="form-control btn-round" v-model="levelClass">
                                        <option value=""><span v-if="loadingLevelClass"><i class="fa fa-spinner"></i> Loading ..</span><span v-else>Choose Class</span></option>
                                        <option v-for="(levelClass, index) in levelClasses" :key="index" :value="levelClass.id" >{{levelClass.name}}</option>
                                    </select>
                                </div>
                                <div class="col-sm-2 text-center">
                                    <button type="submit" class="btn btn-round btn-sm btn-primary col-sm-12" :disabled="levelClass == ''">FETCH</button>
                                </div>
                            </div>
                        </form>
                    </div>

                    <div class="x_panel" >

                        <div v-if="loading" class="col-sm-12" style="min-height: 60px;">
                            <loading style="height:50px;"></loading>
                        </div>
                        <div class="x_content" v-if="classSubjects">
                        
                            <!-- <div v-if="!all">
                                <span>
                                    <button class="btn btn-round btn-sm btn-primary" @click.prevent="allClassSubjects()">All Classes</button>
                                </span>
                            </div> -->
                            <class-subjects :initialClassSubjects="classSubjects" :initialTitle="title" :initialAll="all" @deleteClassSubject="deleteClassSubject($event)" 
                            @selectDeleteMessage="selectDeleteMessage($event)" @editClassSubject="launchEditModal($event)" @all="allClassSubjects()"></class-subjects>
                            <!-- <p v-if="classSubjects == {}">No Data Available</p> -->
                        </div>

                    </div>
                </div>
            </div>
        </div>
        
        <edit-modal :initialClassSubject="classSubject" @updateResponse="updateMessage($event)"></edit-modal>
        
        
    </div>

    
</template>
<script>
import ClassSubjects from '@/components/admin/setting/classSubject/classSubjects';
import EditModal from '@/components/admin/setting/classSubject/editModal';
import Loading from '@/components/utils/loading';
import Modal from '@/components/admin/setting/classSubject/modal';

export default {
    data () {
        return {
            loading: false,
            loadingLevel: false,
            loadingLevelClass: false,
            responseErrors: '',
            error: '',
            title: '',
            classSubjects: {},
            levels: {},
            levelClasses: {},
            levelClass: '',
            level: '',
            klass: '',
            levelId: '',
            message: '',
            all: true,
            fetchedValue: '',
            classSubject: '',
        }
    },
    methods: {
        getAllClassSubjects(){
            this.classSubjects = '';
            this.title = '';
            this.loading = true;
            this.$http.get(`utilities/classSubjects/`)              
           .then(response => {
               if(response){
                   if(response.errors == ''){
                       this.classSubjects = response.data;
                       this.title = 'ALL CLASSES';
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loading = false;
                   this.all = true;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loading = false;
           })

        },
        allClassSubjects(){
            this.fetchedValue = '';
            this.level = '';
            this.levelClass = '';
            this.getAllClassSubjects()
        },
        //Get the levels
        getLevels(){
           this.loadingLevel = true;
           this.$http.get(`utilities/levels/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.levels = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loadingLevel = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loadingLevel = false;
           })
        },
        //On change get the class
        getLevelClass(){
            this.levelClass = '';
            this.loadingLevelClass = true;
            this.$http.get(`utilities/classes/?levelId=${this.level}`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.levelClasses = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loadingLevelClass = false;
               }
           })
           .catch(err => {
               this.error = '';this.error = err;this.loadingLevelClass = false;
           })
        },
        fetchClassSubject(){
            this.classSubjects = '';
            this.title = '';
            this.loading = true;
            this.$http.get(`utilities/classSubjects/?classId=${this.fetchedValue.id}`)             
           .then(response => {
               if(response){
                   if(response.errors == ''){
                       this.classSubjects = response.data;
                       this.title = this.fetchedValue.fullname;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loading = false;
               }
               this.all = false;
           })
           .catch(err => {
               this.error = '';this.error = err;this.loading = false;
           })
        },
        getSpecificClassSubject(){
            let value = this.levelClasses.find(item => item.id == this.levelClass);
            this.fetchedValue = value;
            this.fetchClassSubject();
        },
        
        getClassSubjectsNew(response){
            if(response.status == 'success') {
                this.getAllClassSubjects();
                this.message = 'Class Subject added Successfully';
            } else {
                this.error = response.errors[0]; 
            }
            this.clearMessage();
        },
        launchEditModal(response){
            if(response.errors == '') {
                this.classSubject = response.data[0];
                // this.levelId = this.klass.level.id;
            } else {
                this.responseErrors = response.errors;
            } 
        },
        updateMessage(response){
            if(response.status == 'success'){
                this.message = 'This Class Subject updated successfully!';
                this.clearMessage();
            } else {
                this.error = 'Update was not successful';
                this.clearMessage();
            }
            if(this.fetchedValue == ''){
                this.getAllClassSubjects();
            } else {
                this.fetchClassSubject();
            }
        },
        selectDeleteMessage(e){
            console.log(e);
            if(this.fetchedValue == ''){
                this.getAllClassSubjects();
            } else {
                this.fetchClassSubject();
            }
        },
        deleteClassSubject(response){
            if(response.status == 'success') {
                if(this.fetchedValue == ''){
                    this.getAllClassSubjects();
                } else {
                    this.fetchClassSubject();
                }
                
                this.message = 'This Class Subject deleted Successfully';
                this.clearMessage();
            } else {
                this.responseError = 'This Class Subject could not be deleted';
                this.clearMessage();
            }
        },
        clearMessage(){
            setTimeout(() => {
                this.message = '';
                this.error = '';
                this.responseErrors ='';
            }, 10000);
        }
    },
    created(){
        this.getAllClassSubjects();
        this.getLevels();
    },
    updated(){
        window.jQuery('#datatable').DataTable();
    },
    components: {
        Modal,
        Loading,
        ClassSubjects,
        EditModal
    }
}
    
</script>

<style src="@/assets/datatables.css"></style>



