<template>
<transition name="fade">    
    <div class="">
        <div class="page-title">
            <div class="title_left">
               
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" data-toggle="modal" data-target=".bs-example-modal-lg">ADD NEW TERM</button>
                    <modal @modalMessage="getTermsNew($event)"></modal>
                </div>
                
            </div>
            <div class="title_right">
                <div class="col-md-5 col-sm-5 col-xs-12 form-group pull-right top_search">
                    <div class="input-group">
                    <input type="text" class="form-control" placeholder="Search for..." />

                    <span class="input-group-btn">
                        <button class="btn btn-default" type="button">Go!</button>
                    </span>
                    </div>
                </div>
            </div>
        </div>

        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>
                            Terms
                        </h2>

                        <ul class="nav navbar-right panel_toolbox">
                            <li>
                                <a class="collapse-link">
                                <i class="fa fa-chevron-up"></i>
                                </a>
                            </li>

                        </ul>

                        <div class="clearfix"></div>
                    </div>
                    <div class="x_content">
                        <div v-if="message">
                            <p class="alert alert-success text-center">{{message}}</p>
                        </div>
                        <p v-if="error" class="alert alert-danger text-center">{{error}}</p>
                        <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    </div>

                    <div class="x_panel">
                        <div v-if="loading" class="col-sm-12" style="min-height: 60px;">
                            <loading style="height:50px;"></loading>
                        </div>
                        <div class="x_content" v-if="terms">
                            <table id="datatable" class="table table-striped table-bordered">
                                <thead>
                                    <tr>
                                        <th class="text-center">S/N</th>
                                        <th class="text-center">Term</th>

                                        <th class="text-center">Session</th>


                                        <th class="text-center">Active</th>

                                        <th class="text-center">Edit</th>

                                        <th class="text-center">Delete</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    
                                    <!-- <tr></tr> -->
                                    <term-row v-for="(term, index) in terms" :key="index" :term="term" :index="index" @editTerm="launchEditModal($event)" @deleteTerm="deleteTerm($event)"></term-row>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
        <edit-modal :initialTerm="term" @updateResponse="updateMessage($event)"></edit-modal>
        
        
    </div>

</transition>   
</template>
<script>
import TermRow from '@/components/admin/setting/term/termRow';
import EditModal from '@/components/admin/setting/term/editModal';
import Loading from '@/components/utils/loading';
import Modal from '@/components/admin/setting/term/modal';

export default {
    data () {
        return {
            loading: false,
            responseErrors: '',
            error: '',
            terms: {},
            term: '',
            levelId: '',
            message: '',
        }
    },
    methods: {
        getAllTerms(){
            this.terms = '';
            this.loading = true;
            this.$http.get(`utilities/terms/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.terms = response.data;
                   } else {
                       this.responseError = response.errors;
                   }
                   this.loading = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loading = false;
           })

        },
        getTermsNew(response){
            if(response.status == 'success') {
                this.getAllTerms();
                this.message = 'Term added Successfully';
            } else {
                this.error = response.errors[0]; 
            }
            this.clearMessage();
        },
        launchEditModal(response){
            
            if(response.status == 'success') {
                this.term = response.data[0];
            } else {
                this.responseErrors = response.errors;
            }
            
        },
        updateMessage(response){
            if(response.status == 'success'){
                this.message = 'This Term updated successfully!';
                this.clearMessage();
            } else {
                this.error = 'Update was not successful';
            }
            this.getAllTerms();
        },
        deleteTerm(response){
            if(response.status == 'success') {
                this.getAllTerms();
                this.message = 'This Term deleted Successfully';
                this.clearMessage();
            } else {
                this.responseErrors = response.errors;
            }
        },
        clearMessage(){
            setTimeout(() => {
                this.message = '';
                this.error = '';
                this.responseErrors = '';
            }, 10000);
        }
    },
    created(){
        this.getAllTerms();
    },
    updated(){
        window.jQuery('#datatable').DataTable();
    },
    components: {
        Modal,
        Loading,
        TermRow,
        EditModal
    }
}
    
</script>

<style src="@/assets/datatables.css">
    .fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



