<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Add New Session</h4>
                    </div>
                    <p v-if="errorMessage" class="alert alert-danger">{{errorMessage}}</p>
                    <form @submit.prevent="submitSession" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                            <div class="form-group">
                                <label for="name">Name <span class="required">*</span>
                                </label>
                                <input type="text" id="name" v-model="name" required class="form-control btn-round">
                            </div>
                         
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-sm btn-round btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="isLoading" class="btn btn-round btn-sm btn-primary">Submit</submit-button>
                    </div>
                    </form> 

                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

export default {
    data() {
        return {
            isLoading: false,
            name: '',
            errorMessage: '',
            sessions: {},
        }
    },
    methods: {
        submitSession(){
            this.isLoading = true;
            this.$http.post('settings/session/add', {name:this.name})
            .then((response) => {
                if(response) {
                   this.$emit('modalMessage', response);
                   window.$('.bs-example-modal-lg').modal('hide'); 
                   this.isLoading = false;
                }
            })
            .catch(err => {
                this.errorMessage = err;
                this.isLoading = false;
            })
        },
        
    },
    
    components: {
        SubmitButton
    }
}
</script>


