<template>
    <tr>
        
        <td class="text-center">{{index + 1}}</td>

        <td class="text-center">{{level.name}}</td>
        <td class="text-center">
            <!-- <submit-button class="btn btn-sm btn-round btn-primary" :loading="editLoading" data-toggle="modal" @click="launchEditModal(level.id)">EDIT</submit-button> -->
            <submit-button class="btn btn-sm btn-round btn-primary" :loading="editLoading" data-toggle="modal" @click="launchEditModal(level.id)">EDIT</submit-button>
            
        </td>
        <td class="text-center">
            <submit-button class="btn btn-sm btn-round btn-danger" :loading="deleteLoading" @click="deleteLevel(level.id)">DELETE</submit-button>
        </td>
    </tr>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
import EditModal from '@/components/admin/setting/level/editModal';

export default {
    props: ['index','level'],
    data() {
        return {
           editLoading: false,
           deleteLoading: false, 
        }
    },
    methods: {
        launchEditModal(id){
            
            this.editLoading = true;
            this.$http.get(`utilities/levels/${id}`)
            .then(response => {
                this.$emit('editLevel', response);
                window.$('.edit_modal').modal('show');
                this.editLoading = false;
            })
            .catch(err => {
                this.$emit('editLevel', err);
                this.editLoading = false;
            })
        },
        deleteLevel(id){
            if(confirm('Are you sure, you want to delete this level')){
                this.deleteLoading = true;
                this.$http.delete(`settings/level/delete/${id}`)
                .then((response) => {
                    this.$emit('deleteLevel', response);
                    this.deleteLoading = false; 
                })
                .catch(err => {
                    this.$emit('deleteLevel', err);
                    this.deleteLoading = false;
                })
            }
            
            
        },
    },
    
    components: {
        SubmitButton,
        EditModal,
    }
}

</script>


