import HelloWorld from '@/components/HelloWorld';
import Signin from '@/components/auth/signin';

export default [
    {
        path: '/hello',
        name: 'HelloWorld',
        component: HelloWorld,
    },
    {
        path: '/login',
        name: 'login',
        component: Signin,
    },
]



