<template>
    <div class="">
        <div class="page-title">
            <div class="title_left">
                <h3>Create Staff</h3>
            </div>
        </div>
        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>Create New Staff Profile</h2>
                        <ul class="nav navbar-right panel_toolbox">
                            <li><a class="collapse-link"><i class="fa fa-chevron-up"></i></a></li>
                        </ul>
                        <div class="clearfix"></div>
                        
                    </div>
                    <div class="x_content">
                        <div v-if="message">
                            <p class="alert alert-success text-center">{{message}}</p>
                        </div>
                        <p v-if="errors" class="alert alert-danger text-center">{{errors}}</p>
                        <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    </div>


                    <div class="x_content">

                        <i>Please, fill all compulsory fields marked <span class="required">*</span></i>
                        <div class="clearfix"></div>
                        <br>
                        <div class="col-sm-12 bg-info border_bottom" >
                            <h4><strong>Personal Details</strong></h4>
                        </div>
                        
                        <div class="clearfix"></div>
                        <br>
                        <form @submit.prevent="createStaff" data-parsley-validate class="form-horizontal form-label-left">
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="username">Username <span class="required">*</span></label>
                                    <input type="text" id="username" required="required" v-model="formData.userName" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="first-name">First Name <span class="required">*</span></label>
                                    <input type="text" id="first-name" required="required" v-model="formData.firstName" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="last-name">Last Name <span class="required">*</span></label>
                                    <input type="text" id="last-name" v-model="formData.lastName" required="required" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="middle-name">Other Name</label>
                                    <input id="middle-name" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="formData.otherName">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="email">Email <span class="required">*</span></label>
                                    <input id="email" required class="form-control col-md-7 col-xs-12 btn-round" type="email" v-model="formData.email">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="telephone">Telephone <span class="required">*</span></label>
                                    <input id="telephone" required class="form-control col-md-7 col-xs-12 btn-round" type="tel" v-model="formData.telephone">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="sex">Sex <span class="required">*</span></label>
                                    <select id="sex" class="btn-round form-control" v-model="formData.sexId">
                                        <option value=""><span v-if="loadingSexes"> Loading ..</span><span v-else>Choose Sex</span>  </option>
                                        <option v-for="(sex, index) in sexes" :key="index">{{sex.name}}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="religion">Religion</label>
                                    <select id="religion" class="btn-round form-control" v-model="formData.religionId">
                                        <option value=""><span v-if="loadingReligions"> Loading..</span><span v-else>Choose Religion</span>  </option>
                                        <option v-for="(religion, index) in religions" :key="index" :value="religion.id">{{religion.name}}</option>
                                    </select>
                                </div>
                            </div>
                            
                            <div class="clearfix"></div>
                            <br>
                            <div class="col-sm-12 bg-info border_bottom" >
                                <h4><strong>Contact Details</strong></h4>
                            </div>
                            
                            <div class="clearfix"></div>
                            <br>
                            
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="state">State </label>
                                    <select id="state" class="btn-round form-control" v-model="formData.stateId">
                                        <option value=""><span v-if="loadingStates"> Loading ..</span><span v-else>Choose State</span>  </option>
                                        <option v-for="(staffState, index) in states" :key="index">{{staffState.name}}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="lga">LGA</label>
                                    <select id="lga" class="btn-round form-control" v-model="formData.lgaId">
                                        <option value="">Choose LGA</option>
                                        <option>Christian</option>
                                        <option>Muslim</option>
                                        <option>Aetheist</option>
                                        <option>Buddist</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="contactAd">Contact Address <span class="required">*</span></label>
                                    <input id="contactAd" required class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="formData.contactAddress">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="permanentAd">Permanent Home Address</label>
                                    <input id="permanentAd" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="formData.homeAddress">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="homeTown">Home Town</label>
                                    <input id="homeTown" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="formData.homeTown">
                                </div>
                            </div>
                            <div class="clearfix"></div>
                            <br>
                            <div class="col-sm-12 bg-info border_bottom" >
                                <h4><strong>Staff Details</strong></h4>
                            </div>
                            <div class="clearfix"></div>
                            <br>
                                
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="bank">Bank <span class="required">*</span></label>
                                    <input type="text" id="bank" required="required" v-model="formData.staff.bank" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="acctNo">Account Number <span class="required">*</span></label>
                                    <input id="acctNo" required class="form-control col-md-7 col-xs-12 btn-round" type="number" v-model="formData.staff.accountNumber">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="password">Password</label>
                                    <input id="password" class="form-control col-md-7 col-xs-12 btn-round" type="password" v-model="formData.password">
                                </div>
                            </div>
                            
                            <div class="clearfix"></div>
                            
                            <div class="col-sm-12">
                                <br>
                                <div class="form-group">
                                    <button :disabled="checkFields || submitting" class="btn btn-round btn-info col-sm-2">SUBMIT <i v-if="submitting" class="fa fa-spin fa-spinner"></i></button>
                                </div>
                                
                            </div>
                        </form>
                        
                    </div>
                </div>

                

            </div>
        </div>

    </div>
</template>

<script>
    export default {
        data(){
            return {
                loadingReligions: false,
                loadingSexes: false,
                loadingStates: false,
                submitting: false,
                message: '',
                errors: '',
                responseErrors: '',
                religions: '',
                sexes: '',
                states: '',
                formData: {
                    userName: '',
                    firstName: '',
                    lastName: '',
                    otherName: '',
                    sexId: '',
                    email: '',
                    telephone: '',
                    religionId: '',
                    dateOfBirth: '',
                    stateId: '',
                    lgaId: '',
                    contactAddress: '',
                    homeAddress: '',
                    homeTown: '',
                    classId: '',
                    staff: {
                        bank: '',
                        accountNumber: '',
                    },
                    password: '',
                }
            }
        },
        computed:{
            checkFields(){
                if(this.formData.firstName == ''){
                    return true;
                } 
                if(this.formData.lastName == ''){
                    return true;
                }
                if(this.formData.userName == ''){
                    return true;
                }

                if(this.formData.email == ''){
                    return true;
                }
                if(this.formData.telephone == ''){
                    return true;
                }
                if(this.formData.sexId == ''){
                    return true;
                }
                if(this.formData.contactAddress == ''){
                    return true;
                }
                if(this.formData.staff.bank == ''){
                    return true;
                }
                if(this.formData.staff.accountNumber == ''){
                    return true;
                }
                if(this.formData.password == ''){
                    return true;
                }
                return false;
            }
        },
        methods: {
            getReligion(){
                this.loadingReligions = true;
                this.$http.get(`utilities/religions`)
                .then(response => {
                    if(response.status == 'success'){
                        this.religions = response.data;
                    }
                    this.loadingReligions = false;
                })
            },
            getSex(){
                this.loadingSexes = true;
                this.$http.get(`utilities/sexes`)
                .then(response => {
                    if(response.status == 'success'){
                        this.sexes = response.data;
                    }
                    this.loadingSexes = false;
                })
            },
            getStates(){
                this.loadingStates = true;
                this.$http.get(`utilities/states`)
                .then(response => {
                    if(response.status == 'success'){
                        this.states = response.data;
                    }
                    this.loadingStates = false;
                })
            },
            createStaff(){
                this.submitting = true;
                this.$http.post(`staff/add`, this.assignValues())
                .then(response => {
                    console.log(response);
                    if(response.status == 'success'){
                        this.message = 'Staff created succesfully!';
                    } else {
                        this.responseErrors = response.errors;
                    }
                    this.submitting = false;
                })
                .catch(err => {
                    this.errors = err;this.submitting = false;
                })
            },
            assignValues(){
                let obj = {
                    password: this.formData.password,
                    Email: this.formData.email,
                    UserName: this.formData.userName,
                    Person:{
                        firstName: this.formData.firstName,
                        lastName: this.formData.lastName,
                        otherName: this.formData.otherName,
                        Email: this.formData.email,
                        Phone: this.formData.telephone,
                        contactAddress: this.formData.contactAddress,
                        homeAddress: this.formData.homeAddress,
                        homeTown: this.formData.homeTown,
                    },
                    lga: {
                        id: '',
                    },
                    religion: {
                        id: this.formData.religionId
                    },
                    sex: {
                        id: this.formData.sexId
                    },
                    state: {
                        id: '',
                    },
                    Staff: {
                        bank: this.formData.staff.bank,
                        accountNumber: this.formData.staff.accountNumber
                    },
                    phone: this.formData.telephone,
                }
                console.log(obj);
                return obj;
                // return JSON.stringify(Object.assign({}, this.formData));
            },
        },
        created(){
            this.getReligion();
            this.getSex();
        }
    }

</script>

<style scoped>
    .required{
        color: red;
    }
    .border_bottom{
        border-bottom: 2px solid rgb(190, 189, 189);
    }
</style>


