<template>
    
    <div class="">
        <div class="page-title">
            <div class="title_left">
               
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" data-toggle="modal" data-target=".bs-example-modal-lg">ADD NEW SESSION</button>
                    <modal @modalMessage="getSessionsNew($event)"></modal>
                </div>
                
            </div>
            <div class="title_right">
                <div class="col-md-5 col-sm-5 col-xs-12 form-group pull-right top_search">
                    <div class="input-group">
                    <input type="text" class="form-control" placeholder="Search for..." />

                    <span class="input-group-btn">
                        <button class="btn btn-default" type="button">Go!</button>
                    </span>
                    </div>
                </div>
            </div>
        </div>

        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>
                            Sessions
                        </h2>


                        <div class="clearfix"></div>
                    </div>
                    <div v-if="message">
                        <p class="alert alert-success text-center">{{message}}</p>
                    </div>
                    <p v-if="error" class="alert alert-danger text-center">{{error}}</p>
                    <p v-if="responseErrors" class="alert alert-danger text-center">
                        <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                    </p>
                    <div class="x_panel">
                        <div v-if="loading" class="col-sm-12" style="min-height: 60px;">
                            <loading style="height:50px;"></loading>
                        </div>
                        <div v-if="sessions" class="x_content">
                            
                            <table id="datatable" class="table table-striped table-bordered">
                                <thead>
                                    <tr>
                                        <th class="text-center">S/N</th>
                                        <th class="text-center">Name</th>
                                        <th class="text-center">Edit</th>
                                        <th class="text-center">Delete</th>
                                    </tr>
                                </thead>

                                <tbody>
                                    <!-- <tr>
                                        
                                    </tr> -->
                                    <session-row v-for="(session, index) in sessions" :key="index" :session="session" :index="index" @editSession="launchEditModal($event)" @deleteSession="deleteSession($event)"></session-row>
                                    
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <edit-modal :session="session" @response="modalMessage($event)"></edit-modal>
    </div>

    
</template>
<script>
import Modal from '@/components/admin/setting/session/modal';
import EditModal from '@/components/admin/setting/session/editModal';
import SessionRow from '@/components/admin/setting/session/sessionRow';
import SubmitButton from '@/components/utils/submitButton';
import Loading from '@/components/utils/loading';

export default {
    data () {
        return {
            loading: false,
            responseErrors: '',
            error: '',
            sessions: {},
            session: '',
            message: '',
        }
    },
    methods: {
        getSessions(){
           this.sessions = '';
           this.loading = true;
           this.$http.get(`utilities/sessions/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.sessions = response.data;
                   } else {
                       this.responseError = response.errors;
                   }
                   this.loading = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loading = false;
           })
        },
        getSessionsNew(response){
            if(response.status == 'success') {
                this.getSessions();
                this.message = 'Session added Successfully';
            } else {
                this.error = response.errors[0]; 
            }
            this.clearMessage();
        },
        launchEditModal(response){
            if(response.status == 'success') {
                this.session = response.data[0];
            } else {
                this.responseError = response.errors;
            }
            
        },
        modalMessage(e){
            if(e.status == 'success'){
                this.message = 'This Session updated successfully!';
                this.clearMessage();
            } else {
                this.error = 'Update was not successful';
            }
            this.getSessions();
        },
        deleteSession(response){
            if(response.status == 'success') {
                this.getSessions();
                this.message = 'This Session deleted Successfully';
                this.clearMessage();
            } else {
                this.responseErrors = response.errors;
                this.clearMessage();
            }
            
        },
        clearMessage(){
            setTimeout(() => {
                this.message = '';
                this.error = '';
                this.responseErrors = '';
            }, 10000);
        }

    },
    created(){
       this.getSessions(); 
    },
    updated(){
        window.jQuery('#datatable').DataTable();
    },
    components: {
        Modal, 
        Loading,
        EditModal,
        SubmitButton,
        SessionRow
    }
}
    
</script>

<style src="@/assets/datatables.css"></style>



