<template>
    <tr>
        <td class="text-center">{{index + 1}}</td>
        <td class="text-center">{{student.fullName}}</td>
        <td class="text-center">{{student.regNo}}</td>
        <td class="text-center">{{student.class.name}}</td>
        <td class="text-center">
            <submit-button class="btn btn-round btn-sm btn-primary paddingButton" @click="launchEdit(student.id)" :loading="editLoading">Edit </submit-button>
        </td>
        <td class="text-center">
            <submit-button class="btn btn-round btn-sm btn-info paddingButton" @click="launchChangeClass(student.id)" :loading="changeClassLoading"> Change Class</submit-button>
        </td>
        <td class="text-center">
            <submit-button class="btn btn-round btn-sm btn-danger paddingButton" :loading="deleteLoading" @click="deleteStudent(student.id)" >Delete</submit-button>
        </td>
    </tr>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

    export default {
        props:['initialStudent','index'],
        data(){
            return {
                editLoading: false,
                deleteLoading: false,
                changeClassLoading: false,
                studentDetail: ''
            }
        },
        methods: {
            launchEdit(id){
                this.editLoading = true;
                this.$http.get(`students/profile/${id}`)
                .then(response => {
                    this.$emit('editStudent', response);
                    window.$('.edit_modal').modal('show');
                    this.editLoading = false;
                })
                .catch(err => {
                    this.$emit('editStudent', err);
                    this.editLoading = false;
                })
            },
            launchChangeClass(id){
                this.changeClassLoading = true;
                this.$http.get(`students/profile/${id}`)
                .then(response => {
                    this.$emit('changeStudentClass', response);
                    window.$('.changeClass_modal').modal('show');
                    this.changeClassLoading = false;
                })
                .catch(err => {
                    this.$emit('changeStudentClass', err);
                    this.changeClassLoading = false;
                })
            },
            deleteStudent(id){
                if(confirm('Are you sure, you want to delete this Student ??')){
                    if(confirm('All the students details will leave your school database, are you sure you want to continue')){
                        this.deleteLoading = true;
                        this.$http.get(`students/profile/${id}`)
                        .then(response => {
                            this.studentDetail = response.data;
                            this.$http.delete(`students/${id}`)
                            .then(deleteResponse => {
                                let arr = [this.studentDetail,deleteResponse];
                                this.$emit('deleteStudent', arr);
                                this.deleteLoading = false;
                            })
                        })
                    }
                }

            },
            getStudentDetails(id){
                this.$http.get(`students/profile/${id}`)
                .then(response => {
                    this.studentDetail = response.data;
                })
            }
        },

        computed:{
            student(){
                return this.initialStudent;
            }
        },
        components:{
            SubmitButton
        }
    }
</script>

<style scoped>
    .required{
        color:red !important;
    }
    .paddingButton{
        padding: 2px 10px;
    }
</style>



