const studentMenus = [
  {
    label: 'Dashboard',
    icon: 'fa-home',
    route: '/student/dashboard',
  },
  {
    label: 'Profile',
    icon: 'fa-edit',
    children: [
      {
        label: 'Profile',
        route: '/student/profile',
      },
      {
        label: 'Edit Profile',
        route: '/student/edit',
      },
    ],
  },
  {
    label: 'Result',
    icon: 'fa fa-file-text-o',
    children: [
      {
        label: 'Current Result',
        route: '/student/checkResult',
      },
      {
        label: 'Previous Result',
        route: '/student/previousResult',
      },
    ],
  },
];

export default studentMenus;



