<template>
    <div class="">
        <div class="page-title">
            <div class="title_left">
                <div class="x_content">
                    <h3>Add/Upload Results</h3>
                </div>

            </div>

        </div>
        <div class="clearfix"></div>
        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                
                <div class="x_panel">
                    <div class="col-sm-6">
                        <div class="x_content">
                            <button type="button" class="btn btn-round btn-primary col-sm-8" data-toggle="modal" data-target=".downloadExcelModal" >
                                DOWNLOAD EXCEL TEMPLATE
                            </button>
                            <download-excel-modal :initialTerms="terms" :initialClasses="classes" :loadingTerms="loadingTerms" :loadingClassess="loadingClasses"></download-excel-modal>

                        </div>
                    </div>
                    <div class="col-sm-6">
                        <div class="x_content">
                            <button type="button" class="btn btn-round btn-primary col-sm-8" data-toggle="modal" data-target=".uploadBulkModal">
                                UPLOAD BULK RESULT
                            </button>
                            <upload-bulk-modal :initialTerms="terms" :initialClasses="classes" :loadingTerms="loadingTerms" :loadingClassess="loadingClasses" 
                            @uploadModalMessage="uploadMessage($event)"></upload-bulk-modal>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_content">
                        <h4 class="col-sm-12">GET UPLOADED RESULT</h4>
                    </div>
                    <div class="x_content">
                        
                        <form @submit.prevent="submitResult">
                            
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="term">Term<span class="required"> *</span></label>
                                    <select class="form-control btn-round" v-model="formData.termId" id="term">
                                        <option value=""><span v-if="loadingTerms"><i class="fa fa-spinner fa-spin"></i> Loading..</span><span v-else>Choose Term</span>  </option>
                                        <option v-for="(term, index) in terms" :key="index" :value="term.id">{{term.termType.name}} Term, {{term.session.name}}</option> 
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="klass">Class<span class="required"> *</span></label>
                                    <select class="form-control btn-round"  @change="getSubjects()" v-model="formData.classId" id="klass">
                                        <option value=""><span v-if="loadingClasses">Loading..</span><span v-else>Choose Class</span> </option>
                                        <option v-for="(klass, index) in classes" :key="index" :value="klass.id">{{klass.fullname}}</option> 
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="subject">Subject<span class="required"> *</span></label>
                                    
                                    <select class="form-control btn-round" :disabled="!formData.classId" v-model="formData.subjectId" id="subject">
                                        <option value=""><span v-if="loadingSubjects"> Loading..</span><span v-else>Choose Subject</span>  </option>
                                        <option v-for="(subject, index) in subjects" :key="index" :value="subject.id">{{subject.subjectName}}</option> 
                                    </select>
                                </div>
                            </div>

                            <div class="form-group">
                                <div class="col-sm-12">
                                    <button class="btn-round btn-primary btn col-sm-offset-5 col-sm-2" :disabled="!formData.subjectId || !formData.termId || loadingResults">Get Results 
                                        <i v-if="loadingResults" class="fa fa-spin fa-spinner"></i> 
                                    </button>
                                </div>
                            </div>
                        </form>
                        
                        
                    </div>

                </div>

            </div>
        </div>

        <div class="x_content">
            <div v-if="message">
                <p class="alert alert-success text-center">{{message}}</p>
            </div>
            <p v-if="errors" class="alert alert-danger text-center">{{errors}}</p>
            <p v-if="responseErrors" class="alert alert-danger text-center">
                <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
            </p>
        </div>
        <div class="clearfix"></div>       
        <div class="row" v-if="computedResults">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <loading v-if="loadingResults"></loading>
                    <div class="x_title">
                        <h2>{{subjectName.subjectName}} | {{className.fullname}} <small>{{termName.termType.name}} Term, {{termName.session.name}}</small></h2>
                        
                        
                        <ul class="nav navbar-right panel_toolbox">
                            <li><a class="collapse-link"><i class="fa fa-chevron-up"></i></a></li>
                        </ul>
                        <div class="clearfix"></div>
                    </div>
                    <!-- Results {{computedResults}} |  {{this.$store.state.results}} -->
                    <div class="x_content">
                        
                        <!-- Result {{results}} and Computed property {{computedResults}} -->
                        <table id="datatable" class="table table-striped table-bordered">
                            <thead>
                                <tr>
                                    <th class="text-center">S/N</th>
                                    <th class="text-center">Action</th>
                                    <th class="text-center">Reg No</th>
                                    <th class="text-center">Full Name</th>
                                    <th class="text-center">CA1</th>
                                    <th class="text-center">CA2</th>
                                    <th class="text-center">CA3</th>
                                    <th class="text-center">CA4</th>
                                    <th class="text-center">Exam</th>
                                </tr>
                            </thead>


                            <tbody>
                                
                                <result-row v-for="(result, index) in computedResults" :key="index" :index="index" :initialResult="result" :initialClassData="className"></result-row>

                                
                            </tbody>

                            <tfoot>
                                <tr>
                                    <th class="text-center">S/N</th>
                                    <th class="text-center">Action</th>
                                    <th class="text-center">Reg No</th>
                                    <th class="text-center">Full Name</th>
                                    <th class="text-center">CA1</th>
                                    <th class="text-center">CA2</th>
                                    <th class="text-center">CA3</th>
                                    <th class="text-center">CA4</th>
                                    <th class="text-center">Exam</th>
                                </tr>
                            </tfoot>
                        </table>
                    </div>
                </div>
            </div>
        </div>

           

    </div>
</template>

<script>
import Loading from '@/components/utils/loading';
import ResultRow from '@/components/admin/result/resultRow';

import uploadBulkModal from '@/components/admin/result/uploadBulkModal';
import DownloadExcelModal from '@/components/admin/result/downloadExcelModal';

import { mapState, mapActions, mapMutations } from 'vuex';
    export default {
        data(){
            return {
                terms:'',
                classes: '',
                subjects: '',
                message: '',
                errors: '',
                responseErrors: '',
                loadingTerms: false,
                loadingClasses: false,
                loadingSubjects: false,
                loadingResults: false,
                responseErrors: '',
                formData: {
                    termId: '',
                    classId: '',
                    subjectId: '',
                },
                results: '',
            }
        },
        computed:{
            computedResults(){
                return this.$store.state.results;
            },
            selectedSubject(){
                return this.subjects.find(item => item.id == this.formData.subjectId);
            },
            selectedClass(){
                return this.classes.find(item => item.id == this.formData.classId);
            },
            selectedTerm(){
                return this.terms.find(item => item.id == this.formData.termId);
            },
            subjectName(){
                // if(this.formData.subjectId !== ''){
                //     return this.subjects.find(item => item.id == this.formData.subjectId);
                // } else {
                //     return this.$store.state.resultHeadings.subjectName;
                //     // return ''; 
                // }
                return this.$store.state.resultHeadings.subjectName;
                
            },
            className(){
                return this.$store.state.resultHeadings.className;
                
            },
            termName(){
                // if(this.formData.termId !== ''){
                //    return this.terms.find(item => item.id == this.formData.termId); 
                // } else {
                //     return this.$store.state.resultHeadings.termName;
                //     // return '';
                // }
                return this.$store.state.resultHeadings.termName;
                
            }
        },
        methods: {
            getAllTerms(){
                this.loadingTerms = true;
                this.$http.get(`utilities/terms/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.terms = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingTerms = false;
                    }
                })
                .catch(err => {
                    this.error = '';
                    this.error = err;
                    this.loadingTerms = false;
                })
            },
            getAllClasses(){
                this.loadingClasses = true;
                this.$http.get(`utilities/classes/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.classes = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingClasses = false;
                    }
                })
                .catch(err => {
                    this.error = '';
                    this.error = err;
                    this.loadingClasses = false;
                })

            },
            // On change get the subjects for the particular class
            getSubjects(){
                this.formData.subjectId = '';
                this.loadingSubjects = true;
                this.$http.get(`utilities/classSubjects/?classId=${this.formData.classId}`) 
                .then(response => {
                    if(response){
                        if(response.errors == ''){
                            this.subjects = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingSubjects = false;
                    }
                })
                .catch(err => {
                    this.error = '';this.error = err;this.loadingSubjects = false;
                })
            },
            uploadMessage(response){
                this.message = 'Result Upload was successful';
                this.clearMessage();
            },
            submitResult(){
                this.loadingResults =  true;
                this.$http.get(`results/?termId=${this.formData.termId}&classSubjectId=${this.formData.subjectId}&classId=${this.formData.classId}`)
                .then(response => {
                    if(response.errors == ''){
                        this.results = response.data;
                        this.storeResults(this.results);
                        this.storeResultClassName(this.selectedClass);
                        this.storeResultSubjectName(this.selectedSubject);
                        this.storeResultTermName(this.selectedTerm);
                        
                    } else {
                        this.responseErrors = response.errors;
                    }
                    this.loadingResults = false;
                })
                .catch(err => {
                    this.error = '';this.error = err;
                    this.loadingResults = false;                    
                })
            },
            checkSessionStorage(){
                if(sessionStorage.getItem('results') !== ''){
                    this.storeResults(JSON.parse(sessionStorage.getItem('results')));
                    this.storeResultClassName(JSON.parse(sessionStorage.getItem('resultClassName')));
                    this.storeResultSubjectName(JSON.parse(sessionStorage.getItem('resultSubjectName')));
                    this.storeResultTermName(JSON.parse(sessionStorage.getItem('resultTermName')));
                }
            },
            ...mapActions(['storeResults']),
            ...mapState({
                stateResults:'results',
            }),
            ...mapMutations(['storeResultClassName','storeResultSubjectName','storeResultTermName','storeResults']),
            clearSessionStorage(){
                sessionStorage.removeItem('results');
                sessionStorage.removeItem('resultClassName');
                sessionStorage.removeItem('resultSubjectName');
                sessionStorage.removeItem('resultTermName');
            },
            clearMessage(){
                setTimeout(() => {
                    this.message = '';
                    this.errors = '';
                    this.responseErrors = '';
                }, 10000);
            }
        },
        created(){
            this.getAllTerms();
            this.getAllClasses();
            this.checkSessionStorage();
        },
        updated(){
            window.jQuery('#datatable').DataTable();
        },
        destroyed(){
            this.clearSessionStorage();
        },
        components:{
            Loading,
            ResultRow,
            uploadBulkModal,
            DownloadExcelModal
        }
    }
</script>

<style src="@/assets/datatables.css"></style>


