<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade messageAllModal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Send SMS to all Parents</h4>
                    </div>
                    
                    <p v-if="responseErrors" class="alert alert-danger text-center">
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    <form @submit.prevent="sendMessage" data-parsley-validate enctype="multipart/form-data" ref="form" class="form-horizontal form-label-left">
                    <div class="modal-body"> 


                        <h4>Recipients/Parents</h4>


                        <div class="form-group">
                            <label for="message">Message<span class="required"> *</span></label>
                            <textarea class="form-control btn-round" rows="10" v-model="message"></textarea>
                        </div>
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="loading" :disabled=" !message" class="btn btn-round btn-sm btn-primary">Send Message</submit-button>
                    </div>
                    </form> 
                    
                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
    export default {
        data(){
            return {
                message: '',
                loading: false,
                responseErrors: '',
            }
        },
        methods: {
            sendMessage(){

            }
        },
        created(){
        
        },
        components:{
            SubmitButton
        }
    }
</script>


