<template>
    
    <div class="">
        <div class="page-title">
            <div class="title_left">
               
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" data-toggle="modal" data-target=".bs-example-modal-lg">ADD NEW SUBJECT</button>
                    <modal @modalMessage="getSubjectsNew($event)"></modal>
                </div>
                
            </div>
            <div class="title_right">
                <div class="col-md-5 col-sm-5 col-xs-12 form-group pull-right top_search">
                    <div class="input-group">
                    <input type="text" class="form-control" placeholder="Search for..." />

                    <span class="input-group-btn">
                        <button class="btn btn-default" type="button">Go!</button>
                    </span>
                    </div>
                </div>
            </div>
        </div>

        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <loading v-if="loading"></loading>
                <div class="x_panel">
                    <div class="x_title">
                        <h2>
                            Subjects
                        </h2>

                        <ul class="nav navbar-right panel_toolbox">
                            <li>
                                <a class="collapse-link">
                                <i class="fa fa-chevron-up"></i>
                                </a>
                            </li>

                        </ul>

                        <div class="clearfix"></div>
                    </div>
                    <div v-if="message">
                        <p class="alert alert-success text-center">{{message}}</p>
                    </div>
                    <div v-if="error" class="alert alert-danger text-center">{{error}}</div>
                    <div v-if="responseErrors" class="alert alert-danger text-center">
                        <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                    </div>
                    
                    <div class="x_panel">
                        
                        <div class="x_content" v-if="subjects">
                            <table id="datatable" class="table table-striped table-bordered">
                                <thead>
                                    <tr>
                                        <th class="text-center">S/N</th>
                                        <th class="text-center">Name</th>
                                        <th class="text-center">Edit</th>
                                        <th class="text-center">Delete</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <!-- <tr>
                                        
                                    </tr> -->
                                    <subject-row v-for="(subject, index) in subjects" :key="index" :subject="subject" :index="index" @editSubject="launchEditModal($event)" @deleteSubject="deleteSubject($event)"></subject-row>
                                    
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <th class="text-center">S/N</th>
                                        <th class="text-center">Name</th>
                                        <th class="text-center">Edit</th>
                                        <th class="text-center">Delete</th>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
        <edit-modal :subject="subject" @response="modalMessage($event)"></edit-modal>
    </div>

    
</template>
<script>
import Modal from '@/components/admin/setting/subject/modal';
import EditModal from '@/components/admin/setting/subject/editModal';
import SubjectRow from '@/components/admin/setting/subject/subjectRow';
import SubmitButton from '@/components/utils/submitButton';
import Loading from '@/components/utils/loading';

export default {
    data () {
        return {
            loading: false,
            responseErrors: '',
            error: '',
            subjects: {},
            subject: '',
            message: '',
        }
    },
    methods: {
        getSubjects(){
           this.subjects = '';
           this.loading = true;
           this.$http.get(`utilities/subjects/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.subjects = response.data;
                   } else {
                       this.responseErrors = response.errors;
                   }
                   this.loading = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loading = false;
           })
        },
        getSubjectsNew(response){
            if(response.status == 'success') {
                this.getSubjects();
                this.message = 'Subject added Successfully';
            } else {
                this.error = response.errors[0]; 
            }
            this.clearMessage();
        },
        launchEditModal(response){
            if(response.status == 'success') {
                this.subject = response.data[0];
            } else {
                this.responseErrors = response.errors;
            }
            
        },
        modalMessage(e){
            if(e.status == 'success'){
                this.message = 'This Subject updated successfully!';
                this.clearMessage();
            } else {
                this.error = 'Update was not successful';
            }
            this.getSubjects();
        },
        deleteSubject(response){
            if(response.status == 'success') {
                this.getSubjects();
                this.message = 'This Subject deleted Successfully';
                this.clearMessage();
            } else {
                this.responseErrors = 'This Subject could not be deleted';
            }
            
        },
        clearMessage(){
            setTimeout(() => {
                this.message = '';
                this.error = '';
            }, 10000);
        }

    },
    created(){
       this.getSubjects(); 
    },
    updated(){
        window.jQuery('#datatable').DataTable();
    },
    components: {
        Modal, 
        Loading,
        EditModal,
        SubmitButton,
        SubjectRow
    }
}
    
</script>

<style src="@/assets/datatables.css"></style>



