<template>



  <div class="container body">
      <div class="main_container">
          <div class="col-md-3 left_col">
              <div class="left_col scroll-view">
                  <div class="navbar nav_title" style="border: 0;">
                      <a href="index.html" class="site_title"><i class="fa fa-paw"></i> <span>School Portal</span></a>
                  </div>

                  <div class="clearfix"></div>

                  <!-- menu profile quick info -->
                  <div class="profile clearfix">
                      <div class="profile_pic">
                          <img src="/static/images/logo.png" alt="..." class="img-circle profile_img">
                      </div>
                      <div class="profile_info">
                          <span>Welcome,</span>
                          <h2>{{user.username}}</h2>
                      </div>
                  </div>
                  <!-- /menu profile quick info -->

                  <br />

                <side-bar v-if="sideBar"></side-bar>

                  <!-- /menu footer buttons -->
                  <div class="sidebar-footer hidden-small">
                      <a data-toggle="tooltip" data-placement="top" title="Settings">
                          <span class="glyphicon glyphicon-cog" aria-hidden="true"></span>
                      </a>
                      <a data-toggle="tooltip" data-placement="top" title="FullScreen">
                          <span class="glyphicon glyphicon-fullscreen" aria-hidden="true"></span>
                      </a>
                      <a data-toggle="tooltip" data-placement="top" title="Lock">
                          <span class="glyphicon glyphicon-eye-close" aria-hidden="true"></span>
                      </a>
                      <a data-toggle="tooltip" data-placement="top" title="Logout" @click.prevent="logOut">
                          <span class="glyphicon glyphicon-off" aria-hidden="true"></span>
                      </a>
                  </div>
                  <!-- /menu footer buttons -->
              </div>
          </div>

          <!-- top navigation -->
          <top-nav @signOut="logOut"></top-nav>
          <!-- /top navigation -->

          <!-- page content -->
          <div class="right_col" role="main">
              <!-- top tiles -->
              <router-view></router-view>
              <!-- /top tiles -->
              <br />



          </div>
          <!-- /page content -->

          <!-- footer content -->
          <footer>
              <div class="pull-right">
                  School Portal <a href="https://colorlib.com">Colorlib</a>
              </div>
              <div class="clearfix"></div>
          </footer>
          <!-- /footer content -->
      </div>
  </div>




</template>

<script>

import sideBar from '@/components/sidebar';
import topNav from '@/components/topnav';
import dashboard from '@/components/admin/dashboard/dashboard';
import { mapGetters, mapMutations, mapActions, mapState } from 'vuex';


export default {
  data() {
    return {
        sideBar: false,
    }
  },
  computed:{
      ...mapGetters(['hasToken']),
      ...mapState(['user']),

  },
  methods:{
      checkLocalStorage(){
          let token = localStorage.getItem('token');
          let user = JSON.parse(localStorage.getItem('user'));
          if(!this.hasToken && localStorage.getItem('token')){
            this.signIn({token, user});
          }
      },
      makeSideBarTrue(){
          this.sideBar = true;
      },
      getRef(){
        let customJs = document.createElement('script');
        customJs.setAttribute('src',"../../../../static/gentella/build/js/custom.js");
        document.head.appendChild(customJs);
      },
      logOut(){
        this.signOut;
        this.$router.push('/login');
      },
      ...mapMutations(['signOut']),
      ...mapActions(['signIn'])
  },
  created() {
      this.checkLocalStorage();
      this.makeSideBarTrue();

      this.getRef();

  },
  updated(){
      this.makeSideBarTrue();
  },
  components: {
      sideBar,
      topNav,
      dashboard
  }
}

</script>



