<template>

    <div class="">
        <div class="page-title">
            <div class="title_left">
                <div class="x_content">
                    <h3>View Generate Results</h3>
                </div>

            </div>

        </div>
        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    
                    <div class="x_content">
                        
                        <form @submit.prevent="generatedResult">
                            
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="subject">Session<span class="required"> *</span></label>
                                    
                                    <select class="form-control btn-round" v-model="formData.sessionId" @change="getTerms()" id="term">
                                        <option value=""><span v-if="loadingSessions"> Loading..</span><span v-else>Choose Session</span>  </option>
                                        <option v-for="(session, index) in sessions" :key="index" :value="session.id">{{session.name}}</option> 
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="term">Term<span class="required"> *</span></label>
                                    <select class="form-control btn-round" id="term" :disabled="!formData.sessionId" v-model="formData.termId">
                                        <option value=""><span v-if="loadingTerms"> Loading..</span><span v-else>Choose Term</span>  </option>
                                        <option v-for="(term, index) in terms" :key="index" :value="term.id">{{term.termType.name}}</option> 
                                    </select>
                                </div>
                            </div>

                            <div class="form-group">
                                <div class="col-sm-12">
                                    <button class="btn-round btn-primary btn col-sm-offset-5 col-sm-2" :disabled="!formData.sessionId || !formData.termId || loading">Submit 
                                        <i v-if="loading" class="fa fa-spin fa-spinner"></i> 
                                    </button>
                                </div>
                            </div>
                        </form>
                        
                        
                    </div>

                </div>

            </div>
        </div>

        <div class="clearfix"></div>       
        <div class="row" v-if="generated">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_content">
                        <table id="datatable" class="table table-striped table-bordered">
                            <thead>
                                <tr>
                                    <th class="text-center">S/N</th>
                                    <th class="text-center">Class</th>
                                    <th class="text-center">Term</th>
                                    <th class="text-center">Re-generate</th>
                                    <th class="text-center">Download</th>
                                </tr>
                            </thead>
                            <tbody>
                                <generated-result-row v-for="(result, index) in generated" :key="index" :initialResult="result" :index="index"></generated-result-row>
                            </tbody>
                            <tfoot>
                                <tr>
                                    <th class="text-center">S/N</th>
                                    <th class="text-center">Class</th>
                                    <th class="text-center">Term</th>
                                    <th class="text-center">Re-generate</th>
                                    <th class="text-center">Download</th>
                                </tr>
                            </tfoot>
                        </table>
                    </div>
                </div>
            </div>
        </div>
           

    </div>

</template>

<script>

import GeneratedResultRow from '@/components/admin/result/viewGeneratedResult/generatedResultRow';
    export default {
        data() {
            return {
                loadingSessions: false,
                loadingTerms: false,
                loading: false,
                sessions: '',
                terms: '',
                formData: {
                    sessionId: '',
                    termId: ''
                },
                responseErrors: '',
                errors: '',
                generated: '',
            }
        },
        methods: {
            getSessions(){
                this.loadingSessions = true;
                this.$http.get(`utilities/sessions/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.sessions = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingSessions = false;
                    }
                })
                .catch(err => {
                    this.errors = '';
                    this.errors = err;
                    this.loadingSessions = false;
                })
            },
            getTerms(){
                this.formData.termId = '';
                this.loadingTerms = true;
                this.$http.get(`utilities/terms/?sessionId=${this.formData.sessionId}`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.terms = response.data;
                        } else {
                            this.responseError = response.errors;
                        }
                        this.loadingTerms = false;
                    }
                })
                .catch(err => {
                    this.errors = '';
                    this.errors = err;
                    this.loadingTerms = false;
                })
            },

            generatedResult(){
                this.loading = true;
                this.$http.get(`results/generated?termId=${this.formData.termId}`)
                .then(response => {
                    if(response.status == 'success'){
                        this.generated = response.data;
                    } else {
                        this.responseErrors = response.errors;
                    }
                    this.loading = false;
                })
                .catch(err => {
                    this.errors = '';this.errors = err;this.loading = false;
                })
            }
        },
        created(){
            this.getSessions();
        },
        updated(){
            window.jQuery('#datatable').DataTable();
        },
        components: {
            GeneratedResultRow
        }
    }
</script>


