<template>
    <tr>
        <td class="text-center">{{index + 1}}</td>
        <td class="text-center">
            <p v-if="!editing">
                <submit-button class="btn btn-xs btn-round btn-primary col-sm-10" @click="editResult()" :loading="editLoading">Edit</submit-button>
            </p>
            <p v-else>
                <a @click="updateResult">Update <i v-if="editLoading" class="fa fa-spin fa-spinner"></i></a> | <a @click="cancelEdit">Cancel</a>
            </p>
        </td>
        <td class="text-center"><p v-if="!editing">{{result.regNo}}</p> <p v-else><input type="text" class="col-sm-12" disabled v-model="result.regNo" /></p></td>

        <td class="text-center"><p v-if="!editing">{{result.fullName}}</p><p v-else><input type="text" class="col-sm-12" disabled v-model="result.fullName"/></p></td>

        <td class="text-center"><p v-if="!editing">{{result.results[0].score}}</p>
            <p v-else><input type="number" class="col-sm-12" v-model="modifiedValues[0].score" :max="result.results[0].maxScore" /></p>
        </td>
        <td class="text-center"><p v-if="!editing">{{result.results[1].score}}</p>
            <p v-else><input type="number" class="col-sm-12" v-model="modifiedValues[1].score" :max="result.results[1].maxScore" /></p>
        </td>
        <td class="text-center"><p v-if="!editing">{{result.results[2].score}}</p>
            <p v-else><input type="number" class="col-sm-12" v-model="modifiedValues[2].score" :max="result.results[2].maxScore" /></p>
        </td>
        <td class="text-center"><p v-if="!editing">{{result.results[3].score}}</p>
            <p v-else><input type="number" class="col-sm-12" v-model="modifiedValues[3].score" :max="result.results[3].maxScore" /></p>
        </td>
        <td class="text-center"><p v-if="!editing">{{result.results[4].score}}</p>
            <p v-else><input type="number" class="col-sm-12" v-model="modifiedValues[4].score" :max="result.results[4].maxScore" /></p>
        </td>
        
    </tr>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';


    export default {
        props: ['index','initialResult','initialClassData'],
        data() {
            return {
                editLoading: false,
                editing: false,
                modifiedValues: [],
                errors: [],
            }
        },
        computed:{
            result:{
                get: function() {
                    return this.initialResult;
                },
                set: function(newValue) {
                    return newValue;
                }
                
            }
        },
        mounted(){
            this.modifiedValues = [];
            this.result.results.forEach(result => {
                this.modifiedValues.push(Object.assign({}, result));
            });
            
        },
        methods: {
            editResult(){
                this.editing = true;
            },
            cancelEdit(){
                this.editing = false;
            },
            updateResult(){
                if(confirm('Are you sure, you want to save this score')){
                    this.errors = [];
                    this.modifiedValues.forEach((modified, index) => {
                        if(modified.score > this.result.results[index].maxScore){
                            this.errors.push(modified.name)
                        }
                    })
                    if(this.errors.length !== 0){
                        alert(this.errors+' is/are greater than the max Score');
                    } else {
                        this.editLoading = true;
                        this.$http.post(`results/add`, this.assignedValues())
                        .then(response => {
                            if(response.status == 'success'){
                                console.log(response);
                                this.assignResultToModified();
                                this.modifySessionStorage();
                            }
                            this.editing = false;
                            this.editLoading = false;
                        })
                        .catch(err => {
                            console.log(err);
                            this.editing = false;
                            this.editLoading = false;
                        })
                    }
                    
                }
            },
            assignResultToModified(){
                this.result.results = [];
                this.modifiedValues.forEach(modifiedValue => {
                    this.result.results.push(Object.assign({}, modifiedValue));
                })
            },
            modifySessionStorage(){
                let storageResults = JSON.parse(sessionStorage.getItem('results'));
                storageResults.find(item => {
                    if(item.regNo == this.result.regNo){
                        item.results = this.result.results;
                    }
                });
                // console.log('This is the Storage value ',storageResults);
                sessionStorage.setItem('results', JSON.stringify(storageResults));
                
            },
            testing(){
                let users = [
                    {id: 1, name: "John"},
                    {id: 2, name: "Pete"},
                    {id: 3, name: "Mary"}
                ];
                users.find(item => {
                    if(item.id == 1){

                    }
                })
            },
            assignedValues(){
                let obj = {
                    resultId : this.result.id,
                    regNo : this.result.regNo,
                    subjectId : this.result.subjectId,
                    fullName : this.result.fullName,
                    termId: this.result.termId,
                    CA1: this.modifiedValues[0].score,
                    CA2: this.modifiedValues[1].score,
                    CA3: this.modifiedValues[2].score,
                    CA4: this.modifiedValues[3].score,
                    Exam: this.modifiedValues[4].score,
                    classId: this.initialClassData.id,
                    results: [
                        {
                          name: this.modifiedValues[4].name,
                          score: this.modifiedValues[4].score 
                        },
                        {
                          name: this.modifiedValues[3].name,
                          score: this.modifiedValues[3].score 
                        },
                        {
                          name: this.modifiedValues[2].name,
                          score: this.modifiedValues[2].score 
                        },
                        {
                          name: this.modifiedValues[1].name,
                          score: this.modifiedValues[1].score 
                        },
                        {
                          name: this.modifiedValues[0].name,
                          score: this.modifiedValues[0].score 
                        },
                    ]
                    
                };
                return obj;
            }
        },
        watch:{
            
        },
        components: {
            SubmitButton,
        },
        created(){
            // console.log('Result is',this.result);
        }
    }
</script>
<style>
    a:hover{
        cursor: pointer;
    }
</style>


