<template>
    <div class="">
        <div class="page-title">
            <div class="title_left">
                <h3>SMS</h3>
            </div>
        </div>
        <div class="clearfix"></div>
        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    
                    <button class="btn btn-round btn-primary col-sm-4" data-toggle="modal" data-target=".messageByClassModal" >
                        SEND BY CLASS
                    </button>
                    <message-by-class-modal></message-by-class-modal>
                    <div class="clearfix"></div>
                    
                    <button class="btn btn-round btn-primary col-sm-4" data-toggle="modal" data-target=".messageByLevelModal">
                        SEND BY LEVEL
                    </button>
                    <message-by-level-modal></message-by-level-modal>
                    <div class="clearfix"></div>
                    <button class="btn btn-round btn-primary col-sm-4" data-toggle="modal" data-target=".messageAllModal">
                        SEND TO ALL PARENTS
                    </button>
                    <message-all-modal></message-all-modal>
                    <div class="clearfix"></div>
                        
                    
                </div>
            </div>
        </div>
        
    </div>

</template>

<script>
import MessageByClassModal from '@/components/admin/parent/messageByClassModal';
import MessageByLevelModal from '@/components/admin/parent/messageByLevelModal';
import MessageAllModal from '@/components/admin/parent/messageAllModal';

    export default {
        data(){
            return {

            }
        },
        components:{
            MessageByClassModal,
            MessageByLevelModal,
            MessageAllModal
        }
    }
</script>


