<template>
    
    <div class="">
        <div class="page-title">
            <div class="title_left">
               
                <div class="x_content">
                    <button type="button" class="btn btn-round btn-primary" data-toggle="modal" data-target=".bs-example-modal-lg">ADD NEW TERM TYPE</button>
                    <modal @modalMessage="getTermTypesNew($event)"></modal>
                </div>
                
            </div>
            <div class="title_right">
                <div class="col-md-5 col-sm-5 col-xs-12 form-group pull-right top_search">
                    <div class="input-group">
                    <input type="text" class="form-control" placeholder="Search for..." />

                    <span class="input-group-btn">
                        <button class="btn btn-default" type="button">Go!</button>
                    </span>
                    </div>
                </div>
            </div>
        </div>

        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <div class="x_title">
                        <h2>
                            Term Types
                        </h2>

                        <ul class="nav navbar-right panel_toolbox">
                            <li>
                                <a class="collapse-link">
                                <i class="fa fa-chevron-up"></i>
                                </a>
                            </li>

                        </ul>

                        <div class="clearfix"></div>
                    </div>
                    <div v-if="message">
                        <p class="alert alert-success text-center">{{message}}</p>
                    </div>
                    <p v-if="error" class="alert alert-danger text-center">{{error}}</p>
                    <p v-if="responseErrors" class="alert alert-danger text-center">
                        <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                    </p>
                    <div class="x_panel">
                        <div v-if="loading" class="col-sm-12" style="min-height: 60px;">
                            <loading style="height:50px;"></loading>
                        </div>
                        <div class="x_content" v-if="termTypes">
                            
                            <table id="datatable" class="table table-striped table-bordered">
                                <thead>
                                    <tr>
                                        <th class="text-center">S/N</th>
                                        <th class="text-center">Name</th>
                                        <th class="text-center">Edit</th>
                                        <th class="text-center">Delete</th>
                                    </tr>
                                </thead>

                                <tbody>
                                    <!-- <tr>
                                        
                                    </tr> -->
                                    <term-type-row v-for="(termType, index) in termTypes" :key="index" :termType="termType" :index="index" @editTermType="launchEditModal($event)" @deleteTermType="deleteTermType($event)"></term-type-row>
                                    
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <edit-modal :termType="termType" @response="modalMessage($event)"></edit-modal>
    </div>

    
</template>
<script>
import Modal from '@/components/admin/setting/termType/modal';
import EditModal from '@/components/admin/setting/termType/editModal';
import TermTypeRow from '@/components/admin/setting/termType/termTypeRow';
import SubmitButton from '@/components/utils/submitButton';
import Loading from '@/components/utils/loading';

export default {
    data () {
        return {
            loading: false,
            responseErrors: '',
            error: '',
            termTypes: {},
            termType: '',
            message: '',
        }
    },
    methods: {
        getTermTypes(){
           this.termTypes = '';
           this.loading = true;
           this.$http.get(`utilities/termTypes/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.termTypes = response.data;
                   } else {
                       this.responseError = response.errors;
                   }
                   this.loading = false;
               }
           })
           .catch(err => {
               this.error = '';
               this.error = err;
               this.loading = false;
           })
        },
        getTermTypesNew(response){
            if(response.status == 'success') {
                this.getTermTypes();
                this.message = 'Term Type added Successfully';
            } else {
                this.error = response.errors[0]; 
            }
            this.clearMessage();
        },
        launchEditModal(response){
            if(response.status == 'success') {
                this.termType = response.data[0];
            } else {
                this.responseErrors = response.errors;
                this.clearMessage();
            }
            
        },
        modalMessage(e){
            if(e.status == 'success'){
                this.message = 'This Term Type updated successfully!';
                this.clearMessage();
            } else {
                this.error = 'Update was not successful';
            }
            this.getTermTypes();
        },
        deleteTermType(response){
            if(response.status == 'success') {
                this.getTermTypes();
                this.message = 'This Term Type deleted Successfully';
                this.clearMessage();
            } else {
                this.responseErrors = response.errors;
                this.clearMessage();
            }
            
        },
        clearMessage(){
            setTimeout(() => {
                this.message = '';
                this.error = '';
                this.responseErrors = '';
            }, 10000);
        }

    },
    created(){
       this.getTermTypes(); 
    },
    updated(){
        window.jQuery('#datatable').DataTable();
    },
    components: {
        Modal, 
        Loading,
        EditModal,
        SubmitButton,
        TermTypeRow
    }
}
    
</script>

<style src="@/assets/datatables.css"></style>



