<template>

    <div class="">
        <div class="page-title">
            <div class="title_left">
                <div class="x_content">
                    <h3>Generate Results</h3>
                </div>

            </div>

        </div>
        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    
                    <div class="x_content">
                        
                        <form @submit.prevent="generateResult">
                            
                            
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="klass">Class<span class="required"> *</span></label>
                                    <select class="form-control btn-round" v-model="formData.classId" id="klass">
                                        <option value=""><span v-if="loadingClasses">Loading..</span><span v-else>Choose Class</span> </option>
                                        <option v-for="(klass, index) in classes" :key="index" :value="klass.id">{{klass.fullname}}</option> 
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="subject">Session<span class="required"> *</span></label>
                                    
                                    <select class="form-control btn-round" v-model="formData.sessionId" @change="getTerms()" id="term">
                                        <option value=""><span v-if="loadingSessions"> Loading..</span><span v-else>Choose Session</span>  </option>
                                        <option v-for="(session, index) in sessions" :key="index" :value="session.id">{{session.name}}</option> 
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="term">Term<span class="required"> *</span></label>
                                    <select class="form-control btn-round" id="term" :disabled="!formData.sessionId" v-model="formData.termId">
                                        <option value=""><span v-if="loadingTerms"> Loading..</span><span v-else>Choose Term</span>  </option>
                                        <option v-for="(term, index) in terms" :key="index" :value="term.id">{{term.termType.name}}</option> 
                                    </select>
                                </div>
                            </div>

                            <div class="form-group">
                                <div class="col-sm-12">
                                    <button class="btn-round btn-primary btn col-sm-offset-5 col-sm-2" :disabled="!formData.classId || !formData.sessionId || !formData.termId || loading">Submit 
                                        <i v-if="loading" class="fa fa-spin fa-spinner"></i> 
                                    </button>
                                </div>
                            </div>
                        </form>
                        
                        
                    </div>

                </div>

            </div>
        </div>

        <div class="clearfix"></div>       
        <div class="row" v-if="content">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    <!-- <loading :loading="load"></loading> -->
                    <div class="x_content bs-example-popovers">
                        <div v-if="generatingResult" class="alert alert-info alert-dismissible fade in" role="alert">
                            <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                <span aria-hidden="true"><i class="fa fa-times"></i></span>
                            </button>
                            <b>Generating Result!!</b> The class results are currently being generated. This might take a while ...
                        </div>
                        <div v-if="generated" class="alert alert-success alert-dismissible fade in" role="alert">
                            <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                <span aria-hidden="true"><i class="fa fa-times"></i></span>
                            </button>
                            <b>Success!!</b> results have been successfully generated.
                        </div>
                        
                        <p v-if="compilingResult" class="alert alert-warning alert-dismissible fade in" role="alert">
                            <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                <span aria-hidden="true"><i class="fa fa-times"></i></span>
                            </button>
                            <b>Compiling Result!!</b>The results are currently being prepared for download. This might take a long while ...
                        </p>
                        <p v-if="compiled" class="alert alert-success alert-dismissible fade in" role="alert">
                            <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                <span aria-hidden="true"><i class="fa fa-times"></i></span>
                            </button>
                            <b>Success!!</b>The results are ready for download ...
                        </p>
                        <p v-if="responseErrors" class="alert alert-danger alert-dismissible fade in" role="alert">
                            <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                <span aria-hidden="true"><i class="fa fa-times"></i></span>
                            </button>
                            <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
           

    </div>

</template>

<script>
// import Loading from '@/components/utils/loading';

    export default {
        data(){
            return {
                content: false,
                load: false,
                loading: false,
                loadingClasses: false,
                loadingSessions: false,
                generatingResult: false,
                generated: false,
                compiled: false,
                compilingResult: false,
                loadingTerms: false,
                classes: '',
                sessions: '',
                terms: '',
                errors: '',
                responseErrors: '',
                formData:{
                    classId: '',
                    sessionId: '',
                    termId: '',
                }
            }
        },
        methods: {
            getClasses(){
                this.loadingClasses = true;
                this.$http.get(`utilities/classes/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.classes = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingClasses = false;
                    }
                })
                .catch(err => {
                    this.error = '';
                    this.error = err;
                    this.loadingClasses = false;
                })

            },
            getSessions(){
                this.loadingSessions = true;
                this.$http.get(`utilities/sessions/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.sessions = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingSessions = false;
                    }
                })
                .catch(err => {
                    this.error = '';
                    this.error = err;
                    this.loadingSessions = false;
                })
            },
            getTerms(){
                this.formData.termId = '';
                this.loadingTerms = true;
                this.$http.get(`utilities/terms/?sessionId=${this.formData.sessionId}`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.terms = response.data;
                        } else {
                            this.responseError = response.errors;
                        }
                        this.loadingTerms = false;
                    }
                })
                .catch(err => {
                    this.error = '';
                    this.error = err;
                    this.loadingTerms = false;
                })
            },
            generateResult(){
                this.loading = true;
                this.content = true;
                this.generatingResult = true;
                this.$http.get(`results/generate?classId=${this.formData.classId}&termId=${this.formData.termId}`)
                .then(response => {
                    console.log(response);
                    if(response.status == 'success'){
                        this.generatingResult = false;
                        this.generated = true;
                        this.compileForDownload();
                    }
                })
                .catch(err => {
                    console.log(err)
                    this.errors = err;
                    this.loading = false;
                })
            },
            compileForDownload(){
                this.compilingResult = true;
                this.$http.get(`results/download?classId=${this.formData.classId}&termId=${this.formData.termId}`)
                .then(response => {
                    this.compilingResult = false;
                    if(response.status == 'success'){
                        this.compiled = true;
                    } else {
                        this.responseErrors = response.errors;
                    }
                    console.log(response);
                    this.loading = false;
                })
                .catch(err => {
                    this.errors.push(err);
                    this.compilingResult = false;
                    this.loading = false;
                })
            }
        },
        created(){
            this.getClasses();
            this.getSessions();
        },
        components:{
            // Loading
        }
    }
</script>

<style src="@/assets/datatables.css"></style>


