<template>
    <tr>
        
        <td class="text-center">{{index + 1}}</td>

        <td class="text-center">{{subject.name}}</td>
        <td class="text-center">
            
            <submit-button class="btn btn-sm btn-round btn-primary" :loading="editLoading" data-toggle="modal" @click="launchEditModal(subject.id)">EDIT</submit-button>
            
        </td>
        <td class="text-center">
            <submit-button class="btn btn-sm btn-round btn-danger" :loading="deleteLoading" @click="deleteSubject(subject.id)">DELETE</submit-button>
        </td>
    </tr>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';
import EditModal from '@/components/admin/setting/subject/editModal';

export default {
    props: ['index','subject'],
    data() {
        return {
           editLoading: false,
           deleteLoading: false, 
        }
    },
    methods: {
        launchEditModal(id){
            
            this.editLoading = true;
            this.$http.get(`utilities/subjects/${id}`)
            .then(response => {
                this.$emit('editSubject', response);
                window.$('.edit_modal').modal('show');
                this.editLoading = false;
            })
            .catch(err => {
                this.$emit('editSubject', err);
                this.editLoading = false;
            })
        },
        deleteSubject(id){
            if(confirm('Are you sure, you want to delete this Subject')){
                this.deleteLoading = true;
                this.$http.delete(`settings/subject/delete/${id}`)
                .then((response) => {
                    this.$emit('deleteSubject', response);
                    this.deleteLoading = false; 
                })
                .catch(err => {
                    this.$emit('deleteSubject', err);
                    this.deleteLoading = false;
                })
            }
            
            
        },
    },
    components: {
        SubmitButton,
        EditModal,
    }
}

</script>


