<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade edit_modal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Edit Term</h4>
                    </div>
                    <form @submit.prevent="updateTerm" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                        <div class="form-group">
                            <label for="session">Session <span class="required">*</span></label>
                            
                            <select class="form-control btn-round" id="session" v-model="sessionId">
                                <option value=""><span v-if="loadingSession"><i class="fa fa-spinner"></i> Loading..</span><span v-else>Choose Session</span> </option>
                                <option v-for="(session, index) in sessions" :key="index" :value="session.id">{{session.name}}</option> 
                            </select>
                        </div>
                        
                        <div class="form-group">
                            <label for="termType">Term Type <span class="required">*</span></label>
                            
                            <select class="form-control btn-round" id="termType" v-model="termTypeId">
                                <option value=""><span v-if="loadingTermType"><i class="fa fa-spinner"></i> Loading..</span><span v-else>Choose Term Type</span> </option>
                                <option v-for="(termType, index) in termTypes" :key="index" :value="termType.id">{{termType.name}}</option> 
                            </select>
                        </div>
                        
                        <div class="form-group">
                            <label>Active</label>
                            <input type="checkbox" v-model="term.isActive" />
                        </div>
                          
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-sm btn-round btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="isLoading" class="btn btn-sm btn-round btn-primary">Edit</submit-button>
                    </div>
                    </form> 

                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

export default {
    props:['initialTerm'],
    data() {
        return {
            isLoading: false,
            loadingSession: false,
            loadingTermType: false,
            errorMessage: '',
            sessions: {},
            termTypes: {},
            sessionId: '',
            termTypeId: '',
        }
    },
    computed:{
        term(){
            return this.initialTerm;
        },
        
    },
    methods: {
        getSessions(){
           this.loadingSession = true;
           this.$http.get(`utilities/sessions/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.sessions = response.data;
                   } else {
                       this.responseErrorLevel = response.errors;
                   }
                   this.loadingSession = false;
               }
           })
           .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.loadingSession = false;
           })
        },
        getTermTypes(){
           this.loadingTermType = true;
           this.$http.get(`utilities/termTypes/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.termTypes = response.data;
                   } else {
                       this.responseErrorLevel = response.errors;
                   }
                   this.loadingTermType = false;
               }
           })
           .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.loadingTermType = false;
           })
        },
        updateTerm(){
            this.isLoading = true;
            this.$http.put(`settings/term/update/${this.term.id}`, this.assignValues())
            .then(response => {
                this.$emit('updateResponse', response);
                window.$('.edit_modal').modal('hide');
                this.isLoading = false; 
            })
            .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.isLoading = false;
           })
        },
        assignValues(){
            let obj = {
                session: { id:this.sessionId },
                termType: { id:this.termTypeId },
                isActive: this.term.isActive, 
            }
            return obj;
        },
        
    },
    
    created() {
        this.getTermTypes();
        this.getSessions();
    },
    watch: {
        initialTerm: function(oldValue){
            this.sessionId = oldValue.session.id;
            this.termTypeId = oldValue.termType.id;
        }
    },
    components: {
        SubmitButton,
    }
}
</script>


