<template>
    <div class="">
        <div class="page-title">
            <div class="title_left">
                <div class="x_content">
                    <h3>Students</h3>
                </div>
            </div>
            <div class="title_right">
                <div class="col-md-5 col-sm-5 col-xs-12 pull-right">
                    <router-link class="btn btn-round btn-sm btn-primary col-sm-12" to="/student/add">Create Student</router-link>
                </div>
            </div>
        </div>
        <div class="clearfix"></div>

        <div class="row">
            <div class="col-md-12 col-sm-12 col-xs-12">
                <div class="x_panel">
                    
                    <div class="x_content">
                        
                        <form @submit.prevent="getClassStudent">
                            
                            
                            <div class="form-group">
                                <label for="klass">Class<span class="required"> *</span></label>
                                
                                <select class="form-control btn-round" v-model="classOne" id="klass">
                                    <option value=""><span v-if="loadingClasses"> Loading..</span><span v-else>Choose Class</span>  </option>
                                    <option v-for="(studentClass, index) in classes" :value="studentClass" :key="index">{{studentClass.fullname}}</option> 
                                </select>
                            </div>
                        
                            <div class="form-group">
                                
                                <button class="btn-round btn-primary btn col-sm-2" :disabled="loading">Fetch Students 
                                    <i v-if="loading" class="fa fa-spin fa-spinner"></i> 
                                </button>
                                
                            </div>
                            

                            
                        </form>
                        
                        
                    </div>

                </div>

            </div>
        </div>

        <div class="clearfix"></div> 
        <div class="x_content">
            <div v-if="message">
                <p class="alert alert-success text-center">{{message}}</p>
            </div>
            <p v-if="errors" class="alert alert-danger text-center">{{errors}}</p>
            <p v-if="responseErrors" class="alert alert-danger text-center">
                <span v-for="(error, index) in responseErrors" :key="index">{{error}}&nbsp;</span>
            </p>
        </div>      
        <div class="row">
            
            <div class="col-md-12 col-sm-12 col-xs-12">
            
                <loading v-if="loadingStudents" style="height: 60px;"></loading>
                <div class="x_panel">
                    <h4>{{heading}} <small v-if="heading !== 'All Students'"><button class="btn btn-round btn-info btn-xs" @click="getAllStudents">Get all Students</button> </small> </h4>
                    <div class="x_content" v-if="students">
                        <table id="datatable" class="table table-striped table-bordered">
                            <thead>
                                <tr>
                                    <th class="text-center">S/N</th>
                                    <th class="text-center">Full Name</th>
                                    <th class="text-center">Reg No</th>
                                    <th class="text-center">Class</th>
                                    <th class="text-center">Edit</th>
                                    <th class="text-center">Change</th>
                                    <th class="text-center">Delete</th>
                                </tr>
                            </thead>
                            <tbody>
                                <student-row v-for="(student, index) in students" :key="index" :initialStudent="student" :index="index" 
                                @changeStudentClass="launchChangeClass($event)" @editStudent="editStudent($event)" @deleteStudent="deleteStudent($event)"></student-row>
                            </tbody>
                            <tfoot>
                                <tr>
                                    <th class="text-center">S/N</th>
                                    <th class="text-center">Full Name</th>
                                    <th class="text-center">Reg No</th>
                                    <th class="text-center">Class</th>
                                    <th class="text-center">Edit</th>
                                    <th class="text-center">Change</th>
                                    <th class="text-center">Delete</th>
                                </tr>
                            </tfoot>
                        </table>
                    </div>
                    
                </div>
            </div>
        </div>

        <change-class-modal :initialStudent="oneStudent" :initialClassDetails="classDetails" @response="modalChangeClass($event)"></change-class-modal>
        <edit-modal :initialStudent="oneStudent" @response="modalUpdateMessage($event)"></edit-modal>

    </div>

</template>

<script>
import EditModal from '@/components/admin/student/editStudentModal';
import ChangeClassModal from '@/components/admin/student/changeClassModal';

import Loading from '@/components/utils/loading';
import StudentRow from '@/components/admin/student/StudentRow';
    export default {
        data(){
            return {
                loadingStudents: false,
                loadingClasses: false,
                loading: false,
                responseErrors: '',
                errors: '',
                message: '',
                students: '',
                oneStudent: '',
                classDetails: '',
                classes: {},
                classOne: '',
                heading: 'All Students',
            }
        },
        methods:{
            getAllStudents(){
                this.classOne = '';
                this.students = '';
                this.loadingStudents = true;
                // this.$http.get(`students?pageSize=10`)
                this.$http.get(`students`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            console.log('All students response ',response)
                            this.students = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingStudents = false;
                    }
                    this.heading = 'All Students';
                })
                .catch(err => {
                    this.errors = '';this.errors = err;this.loadingStudents = false;this.heading = 'All Students';
                })
            },
            getAllClasses(){
                this.loadingClasses = true;
                this.$http.get(`utilities/classes/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.classes = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingClasses = false;
                    }
                })
                .catch(err => {
                    this.error = '';this.errors = err;this.loadingClasses = false;
                })

            },
            getClassStudent(){
                this.students = '';
                this.loadingStudents = true;
                this.loading = true;
                this.$http.get(`students?classId=${this.classOne.id}`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.students = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingStudents = false;
                        this.loading = false;
                    }
                    this.heading = this.classOne.fullname;
                })
                .catch(err => {
                    this.errors = '';this.errors = err;this.loadingStudents = false;this.loading = false;this.heading = '';
                })
            },
            editStudent(response){
                this.oneStudent = '';
                if(response.status == 'success') {
                    this.oneStudent = response.data;
                } else {
                    this.responseErrors = response.errors;
                }
            },
            launchChangeClass(response){
                this.oneStudent = '';
                if(response.status == 'success') {
                    this.oneStudent = response.data;
                    this.classDetails = this.classes.find(item => item.id == this.oneStudent.class.id);
                } else {
                    this.responseErrors = response.errors;
                }
            },
            modalUpdateMessage(e){
                
                if(e[1].status == 'success'){
                    let editedStudent = e[0];
                    let editedFullName = editedStudent.lastName+' '+editedStudent.firstName+' '+editedStudent.otherName;
                    this.students.find(student => {
                        if (student.id == editedStudent.id){
                            student.fullName = editedFullName;
                        }
                    });
                    this.message = 'This Student updated successfully!';
                    
                } else {
                    this.error = 'Update was not successful';
                }
                this.clearMessage();
                // if(this.classOne == ''){
                //     this.getAllStudents();
                // } else{
                //     this.getClassStudent();
                // }
                
            },
            modalChangeClass(e){
                this.oneStudent.class.name = e.fullname;
                this.students.find(student => {
                    if (student.id == this.oneStudent.id){
                        student.class.name = this.oneStudent.class.name;
                    }
                });
                this.message = 'Class Change was successful!';
                this.clearMessage();
            },
            deleteStudent(e){
                let studentDetail = e[0];
                // console.log('Student Index ',this.students.findIndex(student => student.id == studentDetail.id));
                if(e[1].status == 'success'){
                    let studentIndex = this.students.findIndex(student => student.id == studentDetail.id);
                    this.students.splice(studentIndex, 1);
                    this.message = 'This student was deleted successfully!';
                } else {
                    this.responseErrors = e[1].errors;
                }
                this.clearMessage();
            },
            clearMessage(){
                setTimeout(() => {
                    this.message = '';
                    this.errors = '';
                    this.responseErrors = '';
                }, 10000);
            }

        },
        components:{
            StudentRow,
            Loading,
            EditModal,
            ChangeClassModal
        },
        created(){
            this.getAllStudents();
            this.getAllClasses();
        },
        updated(){
            window.jQuery('#datatable').DataTable();
        },
    }
</script>

<style src="@/assets/datatables.css"></style>


