const TOKEN = 'token';
const USER = 'user';
const RESULTS = 'results';

export default {
    signIn(state, { token, user }) {
        state.token = token;
        state.user = user;

        localStorage.setItem(TOKEN, token);
        localStorage.setItem(USER, JSON.stringify(user));
    },
    signOut(state) {
      state.user = {};
      state.token = '';
      state.results = '';
      localStorage.clear();
    },

    storeResults(state, results) {
        state.results = results;
        sessionStorage.setItem(RESULTS, JSON.stringify(results));
    },
    storeResultClassName(state, className) {
        state.resultHeadings.className = className;
        sessionStorage.setItem('resultClassName', JSON.stringify(className));
    },
    storeResultSubjectName(state, subjectName) {
        state.resultHeadings.subjectName = subjectName;
        sessionStorage.setItem('resultSubjectName', JSON.stringify(subjectName));
    },
    storeResultTermName(state, termName) {
        state.resultHeadings.termName = termName;
        sessionStorage.setItem('resultTermName', JSON.stringify(termName));
    },
}



