<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade edit_modal" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">
                    
                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Edit Student Details</h4>
                    </div>
                    <form @submit.prevent="updateStudent" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        <div v-if="student !== ''">
                            
                            <div class="col-sm-12">
                                <h5>Reg Number: <strong>{{student.regNo}}</strong></h5>
                            </div>
                            
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="first-name">First Name <span class="required">*</span></label>
                                    <input type="text" id="first-name" required="required" v-model="student.firstName" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="last-name">Last Name <span class="required">*</span></label>
                                    <input type="text" id="last-name" v-model="student.lastName" required="required" class="form-control col-md-7 col-xs-12 btn-round">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="middle-name">Other Name</label>
                                    <input id="middle-name" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="student.otherName">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="sex">Sex <span class="required">*</span></label>
                                    <select v-if="student !== ''" id="sex" class="btn-round form-control" v-model="student.sex.id">
                                        <option value=""><span v-if="loadingSexes"> Loading ..</span><span v-else>Choose Sex</span>  </option>
                                        <option v-for="(sex, index) in sexes" :value="sex.id" :key="index">{{sex.name}}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="religion">Religion</label> 
                                    <select v-if="student !== ''" id="religion" class="btn-round form-control" v-model="student.religion.id">
                                        <option value="null"><span v-if="loadingReligions"> Loading..</span><span v-else>Choose Religion</span>  </option>
                                        <option v-for="(religion, index) in religions" :key="index" :value="religion.id">{{religion.name}}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="dob">Date of Birth <span class="required">*</span></label> 
                                    <input id="dob" required class="form-control col-md-7 col-xs-12 btn-round date-picker" type="date" v-model="student.dateOfBirth">
                                </div>
                            </div>
                            <div class="clearfix"></div>
                            <h4 class="text-center"><strong>Contact Details</strong></h4>
                            <hr>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="state">State </label>
                                    <select v-if="student !== ''" id="state" class="btn-round form-control" v-model="student.stateId">
                                        <option value=""><span v-if="loadingStates"> Loading ..</span><span v-else>Choose State</span>  </option>
                                        <option v-for="(studentState, index) in states" :key="index">{{studentState.name}}</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="lga">LGA</label>
                                    <select id="lga" class="btn-round form-control" v-model="student.lgaId">
                                        <option value="">Choose LGA</option>
                                        <option>Christian</option>
                                        <option>Muslim</option>
                                        <option>Aetheist</option>
                                        <option>Buddist</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="contactAd">Contact Address <span class="required">*</span></label>
                                    <input id="contactAd" required class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="student.contactAddress">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="permanentAd">Permanent Home Address</label>
                                    <input id="permanentAd" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="student.homeAddress">
                                </div>
                            </div>
                            <div class="col-sm-6">
                                <div class="form-group">
                                    <label for="homeTown">Home Town</label>
                                    <input id="homeTown" class="form-control col-md-7 col-xs-12 btn-round" type="text" v-model="student.homeTown">
                                </div>
                            </div>
                            <div class="clearfix"></div>
                            
                            <br>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="submitting" class="btn btn-round btn-sm btn-primary">Update</submit-button>
                    </div>
                    </form> 

                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>
</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

    export default {
        props:['initialStudent'],
        data(){
            return {
                loadingClasses: false,
                loadingReligions: false,
                loadingSexes: false,
                loadingStates: false,
                loadinglgas: false,
                isLoading: false,
                submitting: false,
                message: '',
                responseErrors: '',
                religions: '',
                sexes: '',
                states: '',
                lgas: '',
                errors: '',
                classes: '', 
                formData: {
                    sexId: '',
                    religionId: '',
                    dateOfBirth: '',
                    stateId: '',
                    lgaId: '',
                    classId: '', 
                }
            }
        },
        computed:{
            student() {
                const student = Object.assign({}, this.initialStudent);
                student.dateOfBirth = this.formatDate(student.dateOfBirth);
                if(!student.religion){
                    student.religion = {
                        id: null
                    };
                }
                if(!student.sex){
                    student.sex = {
                        id: null
                    };
                }
                return student;
            },
            dateOfBirth:{
                get: function () {
                    return this.formatDate(this.student.dateOfBirth);
                },
                set: function ( newValue ) {
                    this.student.dateOfBirth = newValue;
                }
            },
            religionId:{
                get: function(){
                    return this.student.religion.id; 
                },
                set: function (newValue){
                    this.student.religion.id = newValue;
                } 
            },
            sexId:{
                get: function(){
                    return this.student.sex.id;
                },
                set: function (newValue){
                    this.formData.sexId = newValue;
                }
            },
            
            
            checkFields(){
                if(this.student.firstName == ''){
                    return true;
                } 
                if(this.student.lastName == ''){
                    return true;
                }
                if(this.student.sexId == ''){
                    return true;
                }
                if(this.dateOfBirth == ''){
                    return true;
                }
                if(this.student.contactAddress == ''){
                    return true;
                }
                if(this.student.classId == ''){
                    return true;
                }
                return false;
            }
        },
        methods: {
            
            getAllClasses(){
                this.loadingClasses = true;
                this.$http.get(`utilities/classes/`) 
                .then(response => {
                    if(response){
                        if(response.status == 'success'){
                            this.classes = response.data;
                        } else {
                            this.responseErrors = response.errors;
                        }
                        this.loadingClasses = false;
                    }
                })
                .catch(err => {
                    this.error = '';this.errors = err;this.loadingClasses = false;
                })

            },
            getReligion(){
                this.loadingReligions = true;
                this.$http.get(`utilities/religions`)
                .then(response => {
                    if(response.status == 'success'){
                        this.religions = response.data;
                    }
                    this.loadingReligions = false;
                })
            },
            getSex(){
                this.loadingSexes = true;
                this.$http.get(`utilities/sexes`)
                .then(response => {
                    if(response.status == 'success'){
                        this.sexes = response.data;
                    }
                    this.loadingSexes = false;
                })
            },
            getStates(){
                this.loadingStates = true;
                this.$http.get(`utilities/states`)
                .then(response => {
                    if(response.status == 'success'){
                        this.states = response.data;
                    }
                    this.loadingStates = false;
                })
            },
            formatDate(dateOfBirth) {
                const date = new Date(dateOfBirth);
                let dd = date.getDate();
                let mm = date.getMonth() + 1;
                const yyyy = date.getFullYear();
                if (dd < 10) {
                    dd = `0${dd}`;
                }
                if (mm < 10) {
                    mm = `0${mm}`;
                }
                const formattedDate = `${yyyy}-${mm}-${dd}`;
                // const formattedDate = `${mm}-${dd}-${yyyy}`;
                return formattedDate;
            },
            updateStudent(){
                this.submitting = true;
                this.$http.put(`students/${this.student.id}`, this.assignValues())
                .then(response => {
                    let arr = [this.student,response]
                    console.log(response);
                    this.$emit('response', arr);
                    window.$('.edit_modal').modal('hide');
                    this.submitting = false;
                })
            },
            assignValues(){
                
                let obj = {
                    firstName: this.student.firstName,
                    lastName: this.student.lastName,
                    otherName: this.student.otherName,
                    sexId: this.sexId,
                    religionId: this.religionId,
                    dateOfBirth: this.student.dateOfBirth,
                    stateId: this.student.stateId,
                    lgaId: this.student.lgaId,
                    contactAddress: this.student.contactAddress,
                    homeAddress: this.student.homeAddress,
                    homeTown: this.student.homeTown,
                    classId: this.student.class.id,
                }
                console.log(obj);
                return obj;
            },
        },
        components:{
            SubmitButton
        },
        created(){
            this.getAllClasses();
            this.getReligion();
            this.getSex();
            this.getStates();
            
        },
        updated(){
            
        }
        
    }
</script>


