<template>
    <div class="x_content">
        <!-- modals -->
        <!-- Small modal -->
        <div class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog modal-md">
                <div class="modal-content">

                    <div class="modal-header bg-primary">
                        <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">Add New Term</h4>
                    </div>
                    <p v-if="errorMessage" class="alert alert-danger text-center">{{errorMessage}}</p>
                    <form @submit.prevent="submitTerm" data-parsley-validate class="form-horizontal form-label-left">
                    <div class="modal-body">
                        
                        <div class="form-group">
                            <label for="session">Session <span class="required">*</span></label>
                            
                            <select class="form-control btn-round" v-model="formData.session" id="session">
                                <option value=""><span v-if="loadingSession"><i class="fa fa-spinner"></i> Loading..</span><span v-else>Choose Session</span> </option>
                                <option v-for="(session, index) in sessions" :key="index" :value="session.id">{{session.name}}</option> 
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="termType">Term Type <span class="required">*</span></label>
                            
                            <select class="form-control btn-round" v-model="formData.termType" id="termType">
                                <option value=""><span v-if="loadingTermType"><i class="fa fa-spinner"></i> Loading..</span><span v-else>Choose Term Type</span> </option>
                                <option v-for="(termType, index) in termTypes" :key="index" :value="termType.id">{{termType.name}}</option> 
                            </select>
                        </div>
                        
                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-round btn-sm btn-default" data-dismiss="modal">Close</button>
                        <submit-button :loading="loading" class="btn btn-round btn-sm btn-primary">Submit</submit-button>
                    </div>
                    </form> 
                    
                </div>
            </div>
        </div>
        <!-- /modals -->
    </div>

</template>

<script>
import SubmitButton from '@/components/utils/submitButton';

export default {
    data() {
        return {
            loadingSession: false,
            loadingTermType: false,
            loading: false,
            name: '',
            sessions: {},
            termTypes: {},
            responseErrorLevel: '',
            errorMessage: '',
            formData: {
                session: '',
                termType: '',
            }
        }
    },
    methods: {
        getSessions(){
           this.loadingSession = true;
           this.$http.get(`utilities/sessions/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.sessions = response.data;
                   } else {
                       this.responseErrorLevel = response.errors;
                   }
                   this.loadingSession = false;
               }
           })
           .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.loadingSession = false;
           })
        },
        getTermTypes(){
           this.loadingTermType = true;
           this.$http.get(`utilities/termTypes/`) 
           .then(response => {
               if(response){
                   if(response.status == 'success'){
                       this.termTypes = response.data;
                   } else {
                       this.responseErrorLevel = response.errors;
                   }
                   this.loadingTermType = false;
               }
           })
           .catch(err => {
               errorMessage= '';
               errorMessage = err;
               this.loadingTermType = false;
           })
        },

        submitTerm(){
            this.loading = true;
            this.$http.post('settings/term/add', this.assignValues())
            .then(response => {
                if(response) {
                   this.$emit('modalMessage', response);
                   window.$('.bs-example-modal-lg').modal('hide'); 
                   this.loading = false;
                }
                
            })
            .catch(err => {
                errorMessage = err;
                this.loading = false;
            })
        },
        
        assignValues(){
            let obj = {
                session: { id:this.formData.session },
                termType: { id:this.formData.termType },
            }
            return obj;
            
        }
    },

    created(){
        this.getSessions();
        this.getTermTypes();
    },
    components: {
        SubmitButton,
    }
}
</script>


