import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations';
import actions from './actions';

Vue.use(Vuex);

const state = {
    user: {},
    token: '',
    results: '',
    resultHeadings: {
        className: '',
        subjectName: '',
        termName: '',
    },
};

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters: {
        hasToken(stateObj) {
            return stateObj.token !== '';
        },
    },
});


